import { UICommands } from './commands.js';
import LoadingManager from './ezyvuecore/core/loading/loadingManager.js';
import GltfLoader from './ezyvuecore/engine/three/gltfLoader.js';
import Project from './project.js';
import ProjectImporter from './projectImporter.js';
import UI from './ui/ui.js';
import { ProgressBar } from './ui/progressBar.js';
import { createStartButton } from './ui/startButton.js';
import Viewer from './viewer.js';
import UIAR from './ui/uiAR.js';

const startImg = 'data:image/png;base64,';

function getViewerAtts(el) {
    const atts = {
        projectFile: el.attributes['project-file']?.value,
        glbFile: el.attributes['glb-file']?.value,
        usdzFile: el.attributes['usdz-file']?.value
    };

    return atts;
}

export default function createViewer(el) {
    const atts = getViewerAtts(el);

    if (!atts.projectFile)
        throw new Error('no project file specified!');

    if (!atts.glbFile)
        throw new Error('no glb file specified!');

    el.style.display = 'block';
    el.style.position = 'relative';

    const canvas = document.createElement('canvas');
    canvas.style.width = '100%';
    canvas.style.height = '100%';
    canvas.style.display = 'none';

    el.appendChild(canvas);

    const progressBar = new ProgressBar();
    el.appendChild(progressBar.elem);

    const startBut = createStartButton(startImg, async () => {
        const viewer = new Viewer(canvas, el);

        progressBar.show(true);

        startBut.style.display = 'none';

        let scene, data;

        const loadingManager = new LoadingManager();

        const glbTask = loadingManager.addTask(async () => {
            return new GltfLoader(viewer.world.renderer.renderer).loadFile(atts.glbFile, 
                prog => loadingManager.updateProgress(glbTask, prog),
                err => { throw new Error(`Load ${atts.glbFile} failed with error: ${err}`) },
            ).then(gltf => scene = gltf.scene);
        });

        const importer = new ProjectImporter();

        const projTask = loadingManager.addTask(async () => {
            return new Promise((res) => {
                importer.load(atts.projectFile, viewer.world.renderer.renderer, dat => {
                    data = dat;
                    res(data);
                }, 
                prog => loadingManager.updateProgress(projTask, prog), 
                err => { throw new Error(`Load ${atts.projectFile} failed with error: ${err}`) });
            });
        });

        await loadingManager.load();
            
        const commands = new UICommands(viewer);

        const ui = new UI(data.settings, commands);

        el.appendChild(ui.elem);

        const uiAR = new UIAR();

        el.appendChild(uiAR.elem);

        const project = new Project(scene, data.settings);

        data.envMap && viewer.world.setEnvironmentMap(data.envMap);

        viewer.atts = atts;
        viewer.ui = ui;
        viewer.uiAR = uiAR;
        viewer.project = project;

        canvas.style.display = 'block';

        progressBar.show(false);

    });

    el.appendChild(startBut);
}