const arButtonImg = 'data:image/png;base64,';
const vrButtonImg = 'data:image/png;base64,';
const webArClose = 'data:image/png;base64,';

export default class UIAR {
    constructor() {
        this.elem = document.createElement('div');
        
        this.arButts = document.createElement('div');
        this.arButts.className = 'xr-button-container';

        this.elem.appendChild(this.arButts);

        this.arButt = document.createElement('a');
        this.arButt.target = '_blank';
        this.arButt.rel = 'noopener noreferrer ar';
        this.arButt.style.width = '32px';
        this.arButt.style.height = '20px';
        this.arButt.style.display = 'none';
        this.arButt.className = 'xr-button';

        let img = document.createElement('img');
        img.src = arButtonImg;
        
        this.arButt.appendChild(img);

        this.arButts.appendChild(this.arButt);

        this.webArButt = document.createElement('img');
        this.webArButt.style.width = '32px';
        this.webArButt.style.height = '20px';
        this.webArButt.style.display = 'none';
        this.webArButt.className = 'xr-button';
        this.webArButt.src = arButtonImg;

        this.arButts.appendChild(this.webArButt);

        this.vrButt = document.createElement('img');

        this.vrButt.style.width = '32px';
        this.vrButt.style.height = '20px';
        this.vrButt.style.display = 'none';
        this.vrButt.className = 'xr-button';
        this.vrButt.src = vrButtonImg;

        this.arButts.appendChild(this.vrButt);

        this.webArClose = document.createElement('img');
        this.webArClose.className = 'ar-close-button';
        this.webArClose.style.display = 'none';
        this.webArClose.src = webArClose;

        this.elem.appendChild(this.webArClose);

    }
}