import HelpWindow from './helpWindow.js';
import InfoWindow from './infoWindow.js';
import { MainMenu, MainMenuBarItemId } from './mainMenu.js';
import { OptionsMenu, OptionsMenuID } from './optionsMenu.js';

export default class UI {
    constructor(settings, commands) {
        this.elem = document.createElement('div');

        this.optionsMenu = new OptionsMenu(item => {
            switch(item.id) {
                case OptionsMenuID.Rotate:
                    this.optionsMenu.toggleSelectItem(OptionsMenuID.Rotate);
                   
                    commands.execute('set-auto-rotate', {
                        arg1: this.optionsMenu.getItem(OptionsMenuID.Rotate).selected 
                    });
                    break;
                case OptionsMenuID.CameraReset:
                    commands.execute('reset-camera');
                    break;
                case OptionsMenuID.Fullscreen:
                    commands.execute('toggle-fullscreen');
                    break;
                case OptionsMenuID.EzyVueLink:
                    window.open('https://www.ezyvue.com', '_blank').focus();
                    break;
                case OptionsMenuID.Measurements:
                    commands.execute('toggle-measuretool');
                    break;      
            }
        });

        this.elem.appendChild(this.optionsMenu.elem);
        this.optionsMenu.show(false);

        this.mainMenu = new MainMenu(item => {
            switch (item.id) {
                case MainMenuBarItemId.EzyVueLink:

                    break;
                case MainMenuBarItemId.Help:
                    this.help.elem.style.display = 'block';
                    break;
                case MainMenuBarItemId.Info:
                    this.info.elem.style.display = 'block';
                    break;
                case MainMenuBarItemId.Settings:
                    this.optionsMenu.toggleVisible();
                    break;
            }
        });

        this.elem.appendChild(this.mainMenu.elem);

        this.help = new HelpWindow();
        this.elem.appendChild(this.help.elem);

        this.info = new InfoWindow(settings.state?.project?.title, settings.state?.project?.description);
        this.elem.appendChild(this.info.elem);

        this.mainMenu.showItem(MainMenuBarItemId.Help, settings.state.viewer.menu.showHelp);
        this.mainMenu.showItem(MainMenuBarItemId.Info, settings.state.viewer.menu.showInfo);
        this.mainMenu.showItem(MainMenuBarItemId.Settings, settings.state.viewer.menu.showOptions);
        this.optionsMenu.showItem(OptionsMenuID.Measurements, settings.state.viewer.dimensionsTool.show);

        this.optionsMenu.selectItem(OptionsMenuID.Rotate, settings.state.viewer.autoRotate.enabled)
    }
}