export function getAllMeshes(scene, filterFunc) {
    if(!scene)
        throw new InvalidArgumentError('scene');

    let meshes = [];

    scene.traverse(object => {
        if (object.isMesh) {
            let pass = true;
            if (filterFunc)
                pass = filterFunc(object);
            if (pass)
                meshes.push(object);
        }
    });
    return meshes;
}

export function getSceneObjectCount(root) {
    if(!root)
        throw new InvalidArgumentError('root');

    let count = 0;

    root.traverse(() => {
        ++count;
    });

    return count;
}

export function disposeMesh(object) {
    if(!object)
        return;

    if (object.geometry) {
        object.geometry.dispose();
    }

    if (object.material) {
        object.material.map && object.material.map.dispose();
        object.material.envMap && object.material.envMap.dispose();
        object.material.lightMap && object.material.lightMap.dispose();
        object.material.aoMap && object.material.aoMap.dispose();
        object.material.bumpMap && object.material.bumpMap.dispose();
        object.material.emissiveMap && object.material.emissiveMap.dispose();
        object.material.metalnessMap && object.material.metalnessMap.dispose();
        object.material.normalMap && object.material.normalMap.dispose();
        object.material.displacementMap && object.material.displacementMap.dispose();

        object.material.dispose();
    }
}

export function disposeMeshes(root) {
    if(!root)
        return;

    root.traverse(object => {
        disposeMesh(object);
    })
}

export function createGroundPlane(width, height, receiveShadows = false) {
    var geometry = new PlaneGeometry(width, height);
    var material = new MeshStandardMaterial();

    var plane = new Mesh(geometry, material);

    plane.receiveShadow = receiveShadows;
    plane.rotateX(-Math.PI / 2.0);

    return plane;
}

export function wattsToIntensity(watts) {
    return watts * 0.1;
}

export function intensityToWatts(intensity) {
    return intensity * 10.0;
}

export function traverseTree(object, callback) {
    if(!object)
        return;

    if(callback && callback(object))
        return true;

    for (let child of object.children) {
        if (traverseTree(child, callback))
            return true;
    }
    return false;
}
