export function createBox(w, h, d) {
    const geometry = new BoxGeometry(w, h, d);
    const material = new MeshStandardMaterial();
    return new Mesh(geometry, material);
}

export function createReflectorsFromScene(scene, root) {
    if(!scene || !(scene instanceof Object3D ))
        throw new InvalidArgumentError('scene');

    if(!root || !(root instanceof Object3D ))
        throw new InvalidArgumentError('root');
    
    const reflectors = [];

    root.traverse(o => {
        if (o.name.includes('ReflectionPlane')) {
            o.visible = false;

            const mirror = new Reflector(o.geometry,
                {
                    clipBias: 0.03,
                    textureWidth: 512,
                    textureHeight: 512,
                    color: o.name.includes('Window') ? 0xff3f3f3f : 0xffffffff
                });
            mirror.material.depthWrite = false;
            mirror.position.copy(o.position);
            mirror.rotation.copy(o.rotation);
            mirror.name = o.name;

            scene.add(mirror);

            reflectors.push(mirror);
        }
    });

    return reflectors;
}

export function findModelRoot(object) {
    while (object) {
        if (object.userData && object.userData.root)
            return object;

        object = object.parent;
    }
}

export function loadHDREnvMap(url, renderer, updateProgress) {
    if(!renderer || !(renderer instanceof WebGLRenderer))
        throw new InvalidArgumentError('renderer');

    const pmremGenerator = new PMREMGenerator(renderer);
    pmremGenerator.compileEquirectangularShader();

    return new Promise((resolve, reject) => {
        const loader = new RGBELoader();

        loader.setDataType(FloatType);

            //    .setDataType(UnsignedByteType)
        loader.setPath('')
            .load(url, texture => {
                const renderTarget = pmremGenerator.fromEquirectangular(texture);

                texture.dispose();

                pmremGenerator.dispose();

                resolve(renderTarget);
            },
            progress => {
                updateProgress && updateProgress(progress.loaded / progress.total * 100);
            },
            error => {
                pmremGenerator.dispose();

                reject(error);
            });
    });
}

export function loadCubeTexture(url, onLoaded, onError) {
    return new CubeTextureLoader()
        .setPath(url)
        .load([
            'px.jpg',
            'nx.jpg',
            'py.jpg',
            'ny.jpg',
            'pz.jpg',
            'nz.jpg'
        ], onLoaded, undefined, onError);
}

export function loadMap(url, onProgress) {
    return new Promise((resolve, reject) => {
        new TextureLoader()
            .load(url,
                texture => resolve(texture),
                progress => { onProgress && onProgress(progress.loaded / progress.total * 100) },
                reject);
    });
}

export function loadCubeMap(path, onProgress, filenames) {
    return new Promise((resolve, reject) => {
        new CubeTextureLoader()
            .setPath(path)
            .load(
                filenames,
                texture => resolve(texture),
                progress => { onProgress && onProgress(progress.loaded / progress.total * 100) },
                reject);
    });
}

export function loadImage(url) {
    return new Promise((resolve, reject) => {
        new ImageLoader()
            .load(url, image => {
                resolve(image);
            },
                undefined,
                reject);
    });
}

export function loadScene(loader, url, updateProgress) {
    return new Promise((resolve, reject) => {
        loader.load(url, gltf => {
            resolve(gltf.scene);
        },
            progress => {
                updateProgress && updateProgress(progress.loaded / progress.total * 100);
            },
            error => {
                reject(error);
            });
    });
}

export function createLabelTexture(char) {
    const ctx = document.createElement('canvas').getContext('2d');
    ctx.canvas.width = 64;
    ctx.canvas.height = 64;

    const x = 32;
    const y = 32;

    const radius = 32;
    const startAngle = 0;
    const endAngle = Math.PI * 2;

    ctx.fillStyle = "#000";
    ctx.beginPath();
    ctx.arc(x, y, radius, startAngle, endAngle);
    ctx.fill();

    ctx.strokeStyle = "#ffffff";
    ctx.lineWidth = 3;
    ctx.beginPath();
    ctx.arc(x, y, radius, startAngle, endAngle);
    ctx.stroke();

    ctx.fillStyle = "#ffffff";
    ctx.font = "32px sans-serif";
    ctx.textAlign = "center";
    ctx.textBaseline = "middle";

    ctx.fillText(char + '', x, y);

    return new CanvasTexture(ctx.canvas);
}

export function create3dLabel(num) {
    const texture = createLabelTexture(num);

    texture.minFilter = LinearFilter;
    texture.wrapS = ClampToEdgeWrapping;
    texture.wrapT = ClampToEdgeWrapping;

    const vertices = [0, 0, 0];
    const geometry = new BufferGeometry();
    geometry.setAttribute('position', new Float32BufferAttribute(vertices, 3));

    const material = new PointsMaterial({
        color: 0x80ffffff,
        size: 32,
        sizeAttenuation: false,
        depthTest: false,
        transparent: true,
        map: texture,
    });

    return new Points(geometry, material);
}

export function createGradient(color1, color2, aspect) {
    const vignette = createBackground({
        aspect: aspect,
        colors: [
            new Color(color1),
            new Color(color2)
        ]
    });

    vignette.name = 'gradient';
    vignette.renderOrder = -1;

    return vignette;
}

export function getMaterials(root) {
    const materials = [];

    root.traverse(object => {
        if (object instanceof Mesh) {
            if (!materials.find(m => m === object.material)) {
                materials.push(object.material);
            }
        }
    });

    return materials;
}

export function createBakedShadowPlane(model, lightness) {
    const boundingGeometry = new BoundingGeometry(model);

    const renderTarget = new WebGLRenderTarget(512, 512, { format: RGBAFormat });

    const bakedShadowPlaneMaterial = new MeshBasicMaterial();

    bakedShadowPlaneMaterial.map = renderTarget.texture;
    bakedShadowPlaneMaterial.depthWrite = false;
    bakedShadowPlaneMaterial.transparent = true;
    bakedShadowPlaneMaterial.opacity = 1 - lightness;

    bakedShadowPlaneMaterial.needsUpdate = true;
    
    const xAdjust = boundingGeometry.size.x * 0.2 * 2.0;
    const zAdjust = boundingGeometry.size.z * 0.2 * 2.0;

    const bakedShadowPlane = new Mesh(new PlaneGeometry(boundingGeometry.size.x + xAdjust, boundingGeometry.size.z + zAdjust, 1, 1), bakedShadowPlaneMaterial);

    bakedShadowPlane.rotation.x = Math.PI * -.5;

    return {
        bakedShadowPlane: bakedShadowPlane,
        renderTarget: renderTarget
    };
}

export function createBakedShadow(renderer, model, sceneRoot, bakedShadowLightness, bakedShadowBlur, bakedShadowFalloff) {
   /*  const rot = new Euler();
    rot.copy(model.rotation);

    const scale = new Vector3();
    scale.copy(model.scale);

    const pos = new Vector3();
    pos.copy(model.position);

    model.rotation.set(0, 0, 0);
    model.scale.set(1, 1, 1);
    model.position.set(0, 0, 0);
 */
    const bakedShadow = createBakedShadowPlane(model, bakedShadowLightness);

    sceneRoot.add(bakedShadow.bakedShadowPlane);

    const bounds = new BoundingGeometry(model);

    bakedShadow.bakedShadowPlane.position.y = bounds._bBox.min.y;

    AoBakedShadowRenderer.renderShadow(renderer, sceneRoot, bakedShadow.renderTarget, model,
                                            bakedShadowBlur ?? 0.5, bakedShadowFalloff ?? 0);

    bakedShadow.bakedShadowPlane.material.map = bakedShadow.renderTarget.texture;
/* 
    model.rotation.copy(rot);
    model.scale.copy(scale);
    model.position.copy(pos);
 */
    return bakedShadow;
}



/**
 * Frame a mesh in the viewport (OrbitControls only)
 * TODO: Make generic for all controllers
 * 
 * @param {BoundingGeometry} modelGeometry  -   bounding geomtery of the mesh 
 * @param {OrbitControls} cameraController  -   orbit controls   
 */
export function frameMesh(modelGeometry, cameraController) {
    if (!(cameraController instanceof OrbitControls))
        return;

    const vang = 0 * Math.PI / 180;
    const hang = 0 * Math.PI / 180;

    const x = Math.sin(hang) * Math.cos(vang);
    const y = Math.sin(hang) * Math.sin(vang);
    const z = Math.cos(hang);

    const fov = cameraController.object.fov;

    let d;

    if (cameraController.object.aspect > 1) {
        d = (modelGeometry.boundingSphere.radius) / Math.tan((fov / 2) * Math.PI / 180);
    }
    else {
        d = (modelGeometry.boundingSphere.radius / cameraController.object.aspect) / Math.tan((fov / 2) * Math.PI / 180);
    }

    const camX = modelGeometry.center.x + x * d;
    const camY = modelGeometry.center.y + y * d;
    const camZ = modelGeometry.center.z + z * d;

    cameraController.object.position.set(camX, camY, camZ);

    if (cameraController.meshTarget)
        cameraController.target.copy(cameraController.meshTarget);
    else
        cameraController.target.copy(modelGeometry.center);

    cameraController.object.near = modelGeometry.boundingSphere.radius * 0.01;
    cameraController.object.far = modelGeometry.boundingSphere.radius * 100;
    cameraController.object.updateProjectionMatrix();

    cameraController.minDistance = 0;
    cameraController.maxDistance = modelGeometry.boundingSphere.radius * 10.0;

    cameraController.clearPanOffset();

    cameraController.update();
}
