//import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader';
import { GLTFLoader, revokeTextureUrls } from '../../three/examples/jsm/loaders/GLTFLoader';
import { LoadingManager } from '../../three/build/three';
import { DRACOLoader } from '../../three/examples/jsm/loaders/DRACOLoader';
import { KTX2Loader } from '../../three/examples/jsm/loaders/KTX2Loader';

export default class GltfLoader {
    constructor(renderer) {
        this.loadingManager = new LoadingManager();

        const gltfLoader = new GLTFLoader(this.loadingManager);
        gltfLoader.setCrossOrigin('anonymous');

        const dracoLoader = new DRACOLoader(this.loadingManager);
        dracoLoader.setDecoderPath('./libs/draco/');
        dracoLoader.preload();

        gltfLoader.setDRACOLoader(dracoLoader);

        const ktx2Loader = new KTX2Loader();
        ktx2Loader.detectSupport(renderer);
        ktx2Loader.setTranscoderPath('./libs/basis/');

        gltfLoader.setKTX2Loader(ktx2Loader);

        this.loader = gltfLoader;
    }

    revokeTextures(revoke) {
        revokeTextureUrls(revoke);
    }

    loadFile(url, onProgress, onError) {
        return new Promise((resolve, reject) => {
            this.loader.load(url,
                gltf => {
                    resolve(gltf);
                },
                progress => {
                    onProgress && onProgress(parseInt((progress.loaded * 100.0) / progress.total));
                },
                error => {
                    console.log(`error loading gltf: ${error}`)

                    onError && onError(error);

                    reject();
                });
        });
    }

    parse(buffer, onError) {
        return new Promise((resolve, reject) => {
            this.loader.parse(buffer, '',
                gltf => {
                    resolve(gltf);
                },
                error => {
                    console.log(`error parsing gltf: ${error}`)

                    onError && onError(error);

                    reject();
                });
        });
    }
}