import { Matrix3, Matrix4, Plane, Quaternion, Raycaster, Vector3 } from "../../three/build/three";

const mtx3 = new Matrix3();
const norm = new Vector3();
const vec = new Vector3();
const vec1 = new Vector3();

export default class WebVRController {
	constructor(renderer, scene, camera, target, cameraRig, options) {
		this.onSessionEnded = this.onSessionEnded.bind(this);
		this.onVrButtonClicked = this.onVrButtonClicked.bind(this);

		this.renderer = renderer;
		this.scene = scene;
		this.target = target;
		this.camera = camera;
		this.cameraRig = cameraRig;
		this.options = options ?? {};

		this.tempMatrix = new Matrix4();
		this.raycaster = new Raycaster();
		this.plane = new Plane(new Vector3(0, 1, 0), 0);

		renderer.xr.enabled = true;

		renderer.xr.addEventListener('sessionstart', () => {
			this.baseReferenceSpace = renderer.xr.getReferenceSpace();
		});
	}

	get session() {
		return this.currentSession;
	}

	static async isVrSupported() {
		if (!('xr' in window.navigator))
			return false;

		return navigator.xr.isSessionSupported('immersive-vr');
	}

	onVrButtonClicked() {
		if (this.vrController.session)
			this.vrController.endSession();
		else
			this.vrController.startSession();
	}

	async startSession() {
		this.currentSession = await navigator.xr.requestSession('immersive-vr', {
			optionalFeatures: ['local-floor', 'bounded-floor', 'hand-tracking']
		});

		this.currentSession.addEventListener('end', this.onSessionEnded);

		await this.renderer.xr.setSession(this.currentSession);

		this.sessionStartHandler && this.sessionStartHandler();
	}

	onSessionEnded() {
		if (this.currentSession)
			this.currentSession.removeEventListener('end', this.onSessionEnded);

		this.currentSession = null;

		this.sessionEndHandler && this.sessionEndHandler();
	}

	endSession() {
		if (this.currentSession)
			this.currentSession.end();
	}

	teleport(position) {
		const offsetPosition = { x: - position.x, y: - position.y, z: - position.z, w: 1 };

		const transform = new window.XRRigidTransform(offsetPosition, new Quaternion());
		const teleportSpaceOffset = this.baseReferenceSpace.getOffsetReferenceSpace(transform);

		this.renderer.xr.setReferenceSpace(teleportSpaceOffset);
	}

	update() {
		this.camera.getWorldPosition(vec);

		this.plane.constant = vec.y - 1.6;

		this.intersection = null;

		this.camera.getWorldDirection(norm);

		this.raycaster.ray.direction.copy(norm);
		this.raycaster.ray.origin.copy(this.camera.getWorldPosition(vec));

		if (norm.y < -0.5) {
			if (this.options.rayGeometryTest) {
				const intersects = this.raycaster.intersectObjects([this.target]);

				if (intersects.length > 0) {
					for (let intersect of intersects) {
						if (intersect.face) {
							mtx3.getNormalMatrix(intersect.object.matrixWorld);

							norm.copy(intersect.face.normal);
							norm.applyMatrix3(mtx3);
							norm.normalize();

							if ((1.0 - norm.y) < 0.05) {
								this.intersection = intersect.point;
								break;
							}
						}
					}
				}
			}
			else {

				if (this.raycaster.ray.intersectPlane(this.plane, vec))
					this.intersection = vec;
			}
		}

		this.renderer.render(this.scene, this.camera);
	}
} 