import Renderer from '../../engine/three/renderer'

import {
    Scene,
    Clock,
    Object3D,
    Color,
} from '../../three/build/three';

import { disposeMeshes } from './util';

export default class World {
    constructor(renderer, effectManager, cameraController) {
        this._renderer = renderer;
        this._composer = effectManager;
        
        this._scene = new Scene();
        
        this._sceneRoot = new Object3D();
        this.sceneRoot.name = 'Scene Root';
        this.scene.add(this.sceneRoot);

        this._scene.background = null;

        this._cameraController = cameraController;

        this.renderFrame = this.renderFrame.bind(this);
        this._renderEnabled = true;
        this._renderFrameCallbacks = [];

        this.updateCameraController = true;

        this.clock = new Clock();

        this.renderer.renderer.setAnimationLoop(this.renderFrame);
    }

    get scene() { return this._scene; }
    get sceneRoot() { return this._sceneRoot; }
    get renderer() { return this._renderer; }
    get composer() { return this._composer; }
    get camera() { return this._cameraController.object; }
    get cameraController() { return this._cameraController }

    set cameraController(controller) { 
        this._cameraController = controller; 

        if(this._composer)
            this._composer.setCamera(controller.object);      
    }

    renderScene() {
        if (!this._killed) {
            if(this.composer)
                this.composer.render(this._scene, this.camera);
            else
                this.renderer.render(this._scene, this.camera);
        }
    }

    renderFrame(timestamp, frame) {
        const delta = this.clock.getDelta();

        if(this._renderer.renderer.xr.isPresenting)
            this.xrRenderFrameHandler && this.xrRenderFrameHandler(delta, frame);
        else        
            this.renderScene();

        if (!this._renderEnabled)
            return;

        if (this._cameraController && this.updateCameraController && this._cameraController.enabled)
            this._cameraController.update(delta);

        this._renderFrameCallbacks.forEach(func => {
            func && func(delta, frame);
        });
    }

    enableRender(enable) {
        this._renderEnabled = enable;
    }

    addRenderFrameCallback(func) {
        if (!func)
            throw new Error('Invalid parameter observer')

        this._renderFrameCallbacks.push(func);
    }

    removeRenderFrameObserver(observer) {
        if (!observer)
            throw new Error('Invalid parameter observer')

        this._renderFrameCallbacks.filter(obs => obs !== observer);
    }

    pauseRenderLoop(pause) {
        if (pause)
            this.renderer.renderer.setAnimationLoop(null);
        else
            this.renderer.renderer.setAnimationLoop(this.renderFrame);
    }

    clearSceneRoot() {
        if(this.sceneRoot) {
            disposeMeshes(this.sceneRoot);

            this.sceneRoot.clear();
        }
    }

    setBackground(object) {
        this._scene.background = object;
    }

    setEnvironmentMap(map) {
        this._scene.environment = map;
    }

    setFog(fog) {
        this._scene.fog = fog;
    }

    resetEffectManager() {
        if(!this._composer)
            return;
            
        this._composer.deleteAllPasses();

        const renderPass = this._composer.createRenderPass(this._scene, this._cameraController.object, true);
        this._composer.addPass(renderPass);
    }

    kill() {
        this._killed = true;
    }
}