import { EffectComposer } from '../../three/examples/jsm/postprocessing/EffectComposer';
import { RenderPass } from '../../three/examples/jsm/postprocessing/RenderPass';
import { BokehPass } from '../../three/examples/jsm/postprocessing/BokehPass';
import { UnrealBloomPass } from '../../three/examples/jsm/postprocessing/UnrealBloomPass';
import { Vector2, WebGLRenderer } from '../../three/build/three';
import { InvalidArgumentError } from '../../core/error/exceptions';

export default class EffectManager {
    constructor(renderer) {
        if(!renderer || !(renderer instanceof WebGLRenderer))
            throw new InvalidArgumentError('renderer');

        this._renderer = renderer;

        const vec2 = new Vector2();

        renderer.getSize(vec2);

        this._composer = new EffectComposer(renderer);

        this._composer.setSize(vec2.x, vec2.y);
    }

    dispose() {
        this._composer.passes.forEach(pass => {
            pass.dispose && pass.dispose();
        });
    }

    deleteAllPasses() {
        this.dispose();

        this._composer.passes = [];
    }

    get composer() {
        return this._composer;
    }

    addPass(pass) {
        this._composer.addPass(pass);
    }
    
    removePass(pass) {       
        this._composer.removePass(pass);

        if(this._composer.passes.length > 0)
            this._composer.passes[this._composer.passes.length-1].renderToScreen = true;
    }

    enablePass(pass, enable) {
        pass.enabled = enable;
    }

    setSize(width, height) {
        if(width && height)
            this._composer.setSize(width, height);
    }

    render(scene, camera) {
        this._composer.render(scene, camera);
    }   
    
    createRenderPass(scene, camera, clearAlpha = false) {
		const pass = new RenderPass(scene, camera);

        pass.clearAlpha = clearAlpha;
        
        return pass;
    }

    createDofPass(scene, camera, focus, aperture, blur) {
        return new BokehPass(scene, camera, {
           /*  focus: 1.0,
            aperture: 0.025,
            maxblur: 0.01, */
            focus: focus,
            aperture: aperture,
            maxblur: blur,
            width: 512,
            height: 512,
            aspect: 1 
        });
    }

    createBloomPass(threshold, strength, radius) {
        return new UnrealBloomPass(new Vector2( 1024, 1024 ), strength * 3, radius, threshold);
    }

    setCamera(camera) {
        this._composer.passes.forEach(pass =>{
            if(pass instanceof RenderPass)
                pass.camera = camera;
            else if(pass instanceof BokehPass)
                pass.camera = camera;
        });
    }
}