import BakedShadowPlane from "./ezyvuecore/engine/three/bakedShadowPlane";
import { DepthFadeReflector } from "./ezyvuecore/engine/three/depthFadeReflector";
import { BufferGeometry, CanvasTexture, ClampToEdgeWrapping, Color, DirectionalLight, Float32BufferAttribute, LinearFilter, Mesh, PlaneGeometry, Points, PointsMaterial, ShadowMaterial, sRGBEncoding } from "./ezyvuecore/three/build/three";

export function createDepthFadeReflector(world, planeSize, color, fadeScale, opacity, blur) {
    var planeGeometry = new PlaneGeometry(planeSize, planeSize);

    const reflector = new DepthFadeReflector(planeGeometry,
        world.renderer.renderer,
        world.scene,
        world.camera,
        {
            clipBias: 0.03,
            textureWidth: 1024,
            textureHeight: 1024,
            color: 0xffffffff,
            encoding: sRGBEncoding
        });

    reflector.rotateX(-Math.PI / 2.0);
    reflector.setFadeScale(fadeScale ?? 3);
    reflector.setOpacity(opacity ?? 1);
    reflector.setColor(color ? new Color(color) : new Color('#808080'));
    reflector.enableBlur(blur ?? false);

    return reflector;
}

export function createGradient(camera) {
    const gradient = createGradient(
        '#ffffff',
        '#808080',
        camera.aspect);

    gradient.castShadow = false;
    gradient.name = 'gradient';
    gradient.visible = false;

    return gradient;
}

export function createShadowPlane(size, opacity) {
    const shadowPlaneMaterial = new ShadowMaterial();
    shadowPlaneMaterial.opacity = 1;
    shadowPlaneMaterial.depthWrite = false;
    shadowPlaneMaterial.color.set(0);

    const shadowPlane = new Mesh(new PlaneGeometry(1, 1, 8, 8), shadowPlaneMaterial);
    shadowPlane.receiveShadow = true;
    shadowPlane.rotation.x = Math.PI * -.5;
    shadowPlane.position.set(0, 0.0001, 0);
    shadowPlane.scale.set(size, size, size);

    return shadowPlane;
}

export function createShadowLight(dist, rot, elev) {
    const shadowLight = new DirectionalLight();
    shadowLight.color.set('#ffffff');
    shadowLight.intensity = 3;
    shadowLight.castShadow = true;
    shadowLight.shadow.bias = -0.0015;
    shadowLight.shadow.radius = 1;
    shadowLight.shadow.mapSize.width = 1024;
    shadowLight.shadow.mapSize.height = 1024;
    shadowLight.shadow.camera.near = dist * 0.01;
    shadowLight.shadow.camera.far = dist * 10;
    shadowLight.shadow.camera.left = -dist * 2;
    shadowLight.shadow.camera.right = dist * 2;
    shadowLight.shadow.camera.top = dist * 2;
    shadowLight.shadow.camera.bottom = -dist * 2;
    shadowLight.shadow.camera.updateProjectionMatrix();

    if (elev !== undefined & rot !== undefined) {
        const r = dist;
        const x = r * Math.sin(elev) * Math.cos(rot);
        const z = r * Math.sin(elev) * Math.sin(rot);
        const y = r * Math.cos(elev);

        shadowLight.position.set(x, y, z);
    }
    else {
        shadowLight.position.set(dist * 2, dist * 2, dist * 2);
    }

    shadowLight.lookAt(0, 0, 0);

    //            const helper = new CameraHelper( shadowLight.shadow.camera );
    //            world.sceneRoot.add( helper );

    return shadowLight;
}


export function createBakedShadowPlane(object, renderer, bakedShadowLightness, bakedShadowBlur, bakedShadowFalloff) {
    const bakedShadowPlane = new BakedShadowPlane(renderer, object, bakedShadowLightness, bakedShadowBlur, bakedShadowFalloff);

    object.attach(bakedShadowPlane.mesh);
    object.bakedShadow = bakedShadowPlane;

    return bakedShadowPlane;
}

export function createAnnotationTexture(text, fillColor='#000000', textColor='#ffffff', borderColor='#ffffff', image) {
    const ctx = document.createElement('canvas').getContext('2d');
    ctx.canvas.width = 64;
    ctx.canvas.height = 64;

    if(!image) {
        const x = 32;
        const y = 32;

        const radius = 32;
        const startAngle = 0;
        const endAngle = Math.PI * 2;

        ctx.fillStyle = fillColor;
        ctx.beginPath();
        ctx.arc(x, y, radius, startAngle, endAngle);
        ctx.fill();

        ctx.strokeStyle = borderColor;
        ctx.lineWidth = 4;
        ctx.beginPath();
        ctx.arc(x, y, radius-2, startAngle, endAngle);
        ctx.stroke();

        ctx.fillStyle = textColor;
        ctx.font = "32px sans-serif";
        ctx.textAlign = "center";
        ctx.textBaseline = "middle";

        ctx.fillText(text + '', x, y);
    }
    else {
        ctx.drawImage(image, 0, 0);
    }

    const texture = new CanvasTexture(ctx.canvas);

    texture.minFilter = LinearFilter;
    texture.wrapS = ClampToEdgeWrapping;
    texture.wrapT = ClampToEdgeWrapping;

    return texture;

}

export function createAnnotation(size, buttonText, labelText, fillColor, textColor, borderColor, cameraState) {
    const vertices = [0, 0, 0];

    const geometry = new BufferGeometry();
    geometry.setAttribute('position', new Float32BufferAttribute(vertices, 3));

    const texture = createAnnotationTexture(buttonText, fillColor, textColor, borderColor);

    const material = new PointsMaterial({
        color: 0xffffffff,
        size: size,
        sizeAttenuation: false,
        depthTest: false,
        transparent: true,
        map: texture,
    });

    const points = new Points(geometry, material);
    points.renderOrder = Number.POSITIVE_INFINITY;

    points.userData = {
        buttonText: buttonText,
        labelText: labelText,
        size: size,
        fillColor: fillColor,
        textColor: textColor,
        borderColor: borderColor,
        cameraState: cameraState
    };

    return points;
}

export function updateAnnotation(points, size, buttonText, labelText, fillColor, textColor, borderColor, cameraState) {
    points.material.map.dispose();
    points.material.map = createAnnotationTexture(buttonText, fillColor, textColor, borderColor);

    points.material.size = size;

    points.userData = {
        buttonText: buttonText,
        labelText: labelText,
        size: size,
        fillColor: fillColor,
        textColor: textColor,
        borderColor: borderColor,
        cameraState: cameraState
    };
}