import { InvalidArgumentError } from '../../core/error/exceptions';
import {
    WebGLRenderer,
    sRGBEncoding,
    PCFSoftShadowMap,
    NoToneMapping,
} from '../../three/build/three';

export default class Renderer {
    init(element, options={}) {
        if(!element)
            throw new InvalidArgumentError('element');

        this._renderer = new WebGLRenderer({
            alpha: options.alpha ?? true, 
            antialias: options.antialias ?? true, 
            preserveDrawingBuffer: options.preserveDrawingBuffer ?? true,
            canvas: element
        });

        this._renderer.setSize(element.width, element.width);
        this._renderer.outputEncoding = sRGBEncoding;

        this._renderer.toneMapping  = options.toneMapping ?? NoToneMapping;
        
        this._renderer.physicallyCorrectLights = true;

        this._renderer.setClearColor( 0, 0);

        this._renderer.shadowMap.enabled = false;
        this._renderer.shadowMap.type = PCFSoftShadowMap;
    }

    get renderer() {
        return this._renderer;
    }

    render(scene, camera) {
        this._renderer.render(scene, camera);
    }   
    
    get canvas() {
        return this._renderer.domElement;
    }

    get xr() {
        return this._renderer.xr;
    }
    
    setSize(width, height) {
        this._renderer.setSize(width, height);
    }

    dispose() {
        this._renderer.dispose();
    }
}