export default class LoadingManager
{
    constructor() {
        this.tasks = [];
    }
    
    addTask(func, name = '') {
        const task = {
            load: func, 
            index: this.tasks.length, 
            progress: 0,
            name: name
        }

        this.tasks.push(task);   
        return task;
    }

    updateProgress(task, progress) {
        task.progress = progress;

        if(this.onUpdateProgress)
            this.onUpdateProgress(this.calcOverallProgress());
    }

    calcOverallProgress() {
        if(!this.tasks || !this.tasks.length)
            return 0;

        return (this.tasks.reduce((a, b) => ({progress: a.progress + b.progress})).progress / (this.tasks.length * 100) * 100);
    }

    async load() {
        await Promise.all(this.tasks.map(task => task.load(task)));
    }
}
