import {
  Scene,
  Mesh,
  Material,
  Object3D,
  Points,
  Texture
} from '../../three/build/three';

export default class ResourceTracker {
  constructor() {
    this.resources = new Set();
  }

  track(resource) {
    if (!resource) {
      return resource;
    }

    if (Array.isArray(resource)) {
      resource.forEach(resource => this.track(resource));
      return resource;
    }

    if (resource instanceof Scene) {
      this.track(resource.background);
      this.track(resource.environment);

      this.track(resource.children);
    }
    else if (resource instanceof Mesh) {
      this.track(resource.geometry);
      this.track(resource.material);
      
      if (resource.dispose)
        this.resources.add(resource);

      this.track(resource.children);
    }
    else if (resource instanceof Points) {
      this.track(resource.geometry);
      this.track(resource.material);

      this.track(resource.children);
    }
    else if (resource instanceof Material) {
      this.track(resource.map);
      this.track(resource.lightMap);
      this.track(resource.metalnessMap);
      this.track(resource.alphaMap);
      this.track(resource.displacementMap);
      this.track(resource.normalMap);
      this.track(resource.emissiveMap);
      this.track(resource.bumpMap);
      this.track(resource.envMap);
      this.track(resource.roughnessMap);
      this.track(resource.aoMap);
      this.track(resource.clearcoatMap);
      this.track(resource.clearcoatNormalMap);
      this.track(resource.clearcoatRoughnessMap);

      if (resource.uniforms) {
        for (const value of Object.values(resource.uniforms)) {
          if (value) {
            const uniformValue = value.value;
            if (uniformValue instanceof Texture ||
              Array.isArray(uniformValue)) {
              this.track(uniformValue);
            }
          }
        }
      }
    }
    else if (resource instanceof Object3D) {
      this.track(resource.children);
    }
    else {
      if (resource.dispose) {
        this.resources.add(resource);
        return;
      }
    }

    return resource;
  }

  untrack(resource) {
    this.resources.delete(resource);
  }

  dispose() {
    for (const resource of this.resources) {
      if (resource instanceof Object3D) {
        if (resource.parent)
          resource.parent.remove(resource);
      }

      if (!(resource instanceof Scene)) {
        if(resource.dispose) {
       //   console.log(`disposing resource ${resource.constructor.name}`);
          resource.dispose();
        }
      }
    }

    this.resources.clear();
  }
}
