
export class ProgressBar {
    constructor() {
        this.elem = document.createElement('div');
        this.elem.className = 'progress-container'

        this.perc = document.createElement('div');
        this.perc.className = 'white-outlined-text';
        this.perc.innerText = '0%';

        this.progress = document.createElement('div');
        this.progress.className = 'progress';

        this.bar = document.createElement('div');
        this.bar.className = 'progress-bar';
        this.bar.style.width = 0;

        this.progress.appendChild(this.bar);

        this.elem.appendChild(this.perc);
        this.elem.appendChild(this.progress);

        this.show(false);
    }

    setProgress(perc) {
        perc = parseInt(perc);

        this.perc.innerText = perc + '%';
        this.bar.style.width = perc + '%';

        this.forceRedraw();
    }

    show(show) {
        this.elem.style.display = show ? 'block' : 'none';
    }

    forceRedraw() {
        this.bar.style.display='none';
        this.bar.offsetHeight;
        this.bar.style.display='block';
    }
}