import { InvalidArgumentError } from '../../core/error/exceptions';
import {
	Color,
	LinearFilter,
	Matrix4,
	Mesh,
	PerspectiveCamera,
	RGBAFormat,
	Vector3,
	Vector4,
    DepthTexture,
    Plane,
    UnsignedShortType,
    WebGLRenderTarget,
    ShaderMaterial,
    UniformsUtils,
	MathUtils,
	Points,
	BufferGeometry,
	Scene,
	Camera,
	WebGLRenderer
} from '../../three/build/three';

import { EffectComposer } from '../../three/examples/jsm/postprocessing/EffectComposer.js';
import { RenderPass } from '../../three/examples/jsm/postprocessing/RenderPass.js';
import { SavePass } from '../../three/examples/jsm/postprocessing/SavePass.js';
import { ShaderPass } from '../../three/examples/jsm/postprocessing/ShaderPass.js';
import { HorizontalBlurShader } from '../../three/examples/jsm/shaders/HorizontalBlurShader.js';
import { VerticalBlurShader } from '../../three/examples/jsm/shaders/VerticalBlurShader.js';

export class DepthFadeReflector extends Mesh {
	constructor(geometry, renderer, scene, camera,  options = {}) {
		if(!geometry || !(geometry instanceof BufferGeometry))
			throw new InvalidArgumentError('geometry');

		if(!renderer || !(renderer instanceof WebGLRenderer))
			throw new InvalidArgumentError('renderer');

		if(!scene || !(scene instanceof Scene))
			throw new InvalidArgumentError('scene');

		if(!camera || !(camera instanceof Camera))
			throw new InvalidArgumentError('camera');
			
		super(geometry);

		this.type = 'Reflector';

		options = options || {};

		const color = ( options.color !== undefined ) ? new Color( options.color ) : new Color( 0x7F7F7F );
		const textureWidth = options.textureWidth || 512;
		const textureHeight = options.textureHeight || 512;
		this.clipBias = options.clipBias || 0;
		const shader = options.shader || DepthFadeReflector.ReflectorShader;

		this.camera = camera;
		this.reflectorPlane = new Plane();
		this.normal = new Vector3();
		this.reflectorWorldPosition = new Vector3();
		this.cameraWorldPosition = new Vector3();
		this.rotationMatrix = new Matrix4();
		this.lookAtPosition = new Vector3( 0, 0, - 1 );
		this.clipPlane = new Vector4();
		this.viewport = new Vector4();

		this.view = new Vector3();
		this.target = new Vector3();
		this.q = new Vector4();

		this.textureMatrix = new Matrix4();
		this.virtualCamera = new PerspectiveCamera();

		const parameters = {
			minFilter: LinearFilter,
			magFilter: LinearFilter,
			format: RGBAFormat,
			stencilBuffer: false
		};

		this.renderTarget = this.createRenderTarget( textureWidth, textureHeight, parameters, true );

		this.composer = new EffectComposer(renderer);

		this.composer.renderToScreen = false;

		this.composer.addPass(new RenderPass( scene, this.virtualCamera ));

		this.verticalBlurPass = new ShaderPass(VerticalBlurShader);
		this.horizontalBlurPass = new ShaderPass(HorizontalBlurShader);

		this.composer.addPass(this.verticalBlurPass);
		this.composer.addPass(this.horizontalBlurPass);

		this.renderTarget1 = this.createRenderTarget( textureWidth, textureHeight, parameters );
	
		this.composer.addPass(new SavePass(this.renderTarget1));
		
		const material = new ShaderMaterial( {
			uniforms: UniformsUtils.clone( shader.uniforms ),
			fragmentShader: shader.fragmentShader,
			vertexShader: shader.vertexShader,
			transparent: true,
			depthWrite: false
		} );

		material.uniforms.tDiffuse.value = this.renderTarget1.texture;
		material.uniforms.tDepth.value = this.renderTarget.depthTexture;
		material.uniforms.color.value = color;
		material.uniforms.textureMatrix.value = this.textureMatrix;

		this.material = material;
	}

	createRenderTarget(textureWidth, textureHeight, parameters, createDepthBuff = false) {
		const renderTarget = new WebGLRenderTarget( textureWidth, textureHeight, parameters );

		if ( ! MathUtils.isPowerOfTwo( textureWidth ) || ! MathUtils.isPowerOfTwo( textureHeight ) ) {
			renderTarget.texture.generateMipmaps = false;
		}
		
		if(createDepthBuff) {
			renderTarget.depthBuffer = true;
			renderTarget.depthTexture = new DepthTexture();
			renderTarget.depthTexture.type = UnsignedShortType;
		}

		return renderTarget;
	}

	onBeforeRender( renderer, scene, camera ) {
		this.reflectorWorldPosition.setFromMatrixPosition( this.matrixWorld );
		this.cameraWorldPosition.setFromMatrixPosition( camera.matrixWorld );

		this.rotationMatrix.extractRotation( this.matrixWorld );

		this.normal.set( 0, 0, 1 );
		this.normal.applyMatrix4( this.rotationMatrix );

		this.view.subVectors( this.reflectorWorldPosition, this.cameraWorldPosition );

		// Avoid rendering when reflector is facing away

		if ( this.view.dot( this.normal ) > 0 ) return;

		this.view.reflect( this.normal ).negate();
		this.view.add( this.reflectorWorldPosition );

		this.rotationMatrix.extractRotation( camera.matrixWorld );

		this.lookAtPosition.set( 0, 0, - 1 );
		this.lookAtPosition.applyMatrix4( this.rotationMatrix );
		this.lookAtPosition.add( this.cameraWorldPosition );

		this.target.subVectors( this.reflectorWorldPosition, this.lookAtPosition );
		this.target.reflect( this.normal ).negate();
		this.target.add( this.reflectorWorldPosition );

		this.virtualCamera.position.copy( this.view );
		this.virtualCamera.up.set( 0, 1, 0 );
		this.virtualCamera.up.applyMatrix4( this.rotationMatrix );
		this.virtualCamera.up.reflect( this.normal );
		this.virtualCamera.lookAt( this.target );

		this.virtualCamera.far = camera.far; // Used in WebGLBackground

		this.virtualCamera.updateMatrixWorld();
		this.virtualCamera.projectionMatrix.copy( camera.projectionMatrix );

		this.material.uniforms.cameraNear.value = camera.near;
		this.material.uniforms.cameraFar.value = camera.far;

		this.material.uniformsNeedUpdate = true;

		// Update the texture matrix
		this.textureMatrix.set(
			0.5, 0.0, 0.0, 0.5,
			0.0, 0.5, 0.0, 0.5,
			0.0, 0.0, 0.5, 0.5,
			0.0, 0.0, 0.0, 1.0
		);

		this.textureMatrix.multiply( this.virtualCamera.projectionMatrix );
		this.textureMatrix.multiply( this.virtualCamera.matrixWorldInverse );
		this.textureMatrix.multiply( this.matrixWorld );

		// Now update projection matrix with new clip plane, implementing code from: http://www.terathon.com/code/oblique.html
		// Paper explaining this technique: http://www.terathon.com/lengyel/Lengyel-Oblique.pdf
		this.reflectorPlane.setFromNormalAndCoplanarPoint( this.normal, this.reflectorWorldPosition );
		this.reflectorPlane.applyMatrix4( this.virtualCamera.matrixWorldInverse );

		this.clipPlane.set( this.reflectorPlane.normal.x, this.reflectorPlane.normal.y, this.reflectorPlane.normal.z, this.reflectorPlane.constant );

		const projectionMatrix = this.virtualCamera.projectionMatrix;

		this.q.x = ( Math.sign( this.clipPlane.x ) + projectionMatrix.elements[ 8 ] ) / projectionMatrix.elements[ 0 ];
		this.q.y = ( Math.sign( this.clipPlane.y ) + projectionMatrix.elements[ 9 ] ) / projectionMatrix.elements[ 5 ];
		this.q.z = - 1.0;
		this.q.w = ( 1.0 + projectionMatrix.elements[ 10 ] ) / projectionMatrix.elements[ 14 ];

		// Calculate the scaled plane vector
		this.clipPlane.multiplyScalar( 2.0 / this.clipPlane.dot( this.q ) );

		// Replacing the third row of the projection matrix
		projectionMatrix.elements[ 2 ] = this.clipPlane.x;
		projectionMatrix.elements[ 6 ] = this.clipPlane.y;
		projectionMatrix.elements[ 10 ] = this.clipPlane.z + 1.0 - this.clipBias;
		projectionMatrix.elements[ 14 ] = this.clipPlane.w;

		scene.traverse(o => {
			if(o instanceof Points)
				o.visible = false;
		});

		// Render

		this.visible = false;

		const currentRenderTarget = renderer.getRenderTarget();

		const currentXrEnabled = renderer.xr.enabled;
		const currentShadowAutoUpdate = renderer.shadowMap.autoUpdate;

		// render with depth
		renderer.xr.enabled = false; // Avoid camera modification
		renderer.shadowMap.autoUpdate = false; // Avoid re-computing shadows

		renderer.setRenderTarget( this.renderTarget );
		
		renderer.state.buffers.depth.setMask( true ); // make sure the depth buffer is writable so it can be properly cleared, see #18897

		if ( renderer.autoClear === false ) 
			renderer.clear();

		renderer.render( scene, this.virtualCamera );

		// render and blur
		if(this.blur) {
			this.material.uniforms.tDiffuse.value = this.renderTarget1.texture;

			this.composer.render(0.01);
		}
		else {
			this.material.uniforms.tDiffuse.value = this.renderTarget.texture;
		}

		renderer.xr.enabled = currentXrEnabled;
		renderer.shadowMap.autoUpdate = currentShadowAutoUpdate;

		renderer.setRenderTarget( currentRenderTarget );

		// Restore this.viewport
		const bounds = camera.bounds;

		if ( bounds !== undefined ) {

			const size = renderer.getSize();
			const pixelRatio = renderer.getPixelRatio();

			this.viewport.x = bounds.x * size.width * pixelRatio;
			this.viewport.y = bounds.y * size.height * pixelRatio;
			this.viewport.z = bounds.z * size.width * pixelRatio;
			this.viewport.w = bounds.w * size.height * pixelRatio;

			renderer.state.this.viewport( this.viewport );
		}

		this.visible = true;

		scene.traverse(o => {
			if(o instanceof Points)
				o.visible = true;
		});

	}
	
	getRenderTarget() {
		return this.renderTarget;
	}

	setFadeScale(scale) {
		this.material.uniforms.fadeScale.value = scale * this.camera.far / this.camera.near;
	}

	setColor(color) {
		this.material.uniforms.color.value = color;
	}

	setOpacity(opacity) {
		this.material.uniforms.opacity.value = opacity;
	}

	enableBlur(enable) {
		this.blur = enable;
	}

	dispose() {
		this.composer.renderTarget1.dispose();
        this.composer.renderTarget2.dispose();

		this.renderTarget.dispose();
		this.renderTarget1.dispose();
	}
}

DepthFadeReflector.ReflectorShader = {

	uniforms: {

		'color': {
			type: 'c',
			value: null
		},

		'tDiffuse': {
			type: 't',
			value: null
		},
		
		'tDepth': {
			type: 't',
			value: null
		},

		'textureMatrix': {
			type: 'm4',
			value: null
		},
		
		'cameraNear': {
			type: 'f',
			value: 0
		},
		
		'cameraFar': {
			type: 'f',
			value: 0
		},
		'fadeScale': {
			type: 'f',
			value: 3
		},
		'opacity': {
			type: 'f',
			value: 0.5
		},

	},

	vertexShader: [
		'uniform mat4 textureMatrix;',
		'varying vec4 vUv;',

		'void main() {',

		'	vUv = textureMatrix * vec4( position, 1.0 );',

		'	gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );',

		'}'
	].join( '\n' ),

	fragmentShader: [
		'#include <packing>',
		'uniform vec3 color;',
		'uniform sampler2D tDiffuse;',
		'uniform sampler2D tDepth;',
		'uniform float cameraNear;',
		'uniform float cameraFar;',
		'uniform float fadeScale;',
		'uniform float opacity;',
		'varying vec4 vUv;',
		
		'float readDepth( sampler2D depthSampler, vec4 coord ) {',
				
		'	float fragCoordZ = texture2DProj( depthSampler, coord ).x;',
		'	float viewZ = perspectiveDepthToViewZ( fragCoordZ, cameraNear, cameraFar );',
		'	return viewZToOrthographicDepth( viewZ, cameraNear, cameraFar );',
			
		'}',

		'void main() {',
		'	vec4 base = texture2DProj( tDiffuse, vUv );',
		' 	float depth = readDepth( tDepth, vUv );',
		'	gl_FragColor = vec4(base.rgb * color.rgb, (1.0 - ( depth * fadeScale )) * opacity );',
		'}'
	].join( '\n' )
};