import {
    Box3,
    CylinderGeometry,
    Light,
    Matrix4,
    Mesh,
    MeshBasicMaterial,
    MeshPhongMaterial,
    PerspectiveCamera,
    Plane,
    Raycaster,
    RingGeometry,
    Scene,
    Vector2,
    Vector3,
} from '../../three/build/three';
import { XREstimatedLight } from '../../three/examples/jsm/webxr/XREstimatedLight';
import { traverseTree, Vector3_YAxis } from './util';

let hitTestSource = null;
let hitTestSourceRequested = false;
const mtx4 = new Matrix4();
const vec2 = new Vector2();
const vec3 = new Vector3();
const boundingBox = new Box3();

export default class WebArController {
    constructor(renderer, domElem) {
        this.onTouchStart = this.onTouchStart.bind(this);
        this.onTouchMove = this.onTouchMove.bind(this);
        this.onLightEstimationStart = this.onLightEstimationStart.bind(this);
        this.onLightEstimationEnd = this.onLightEstimationEnd.bind(this);

        this.renderer = renderer;
        this.domElem = domElem;

        this.scene = new Scene();

        this.camera = new PerspectiveCamera();
        
        this.scene.add(this.camera);

        renderer.xr.enabled = true;

        this.reticle = new Mesh(
            new RingGeometry(0.25, 0.3, 32).rotateX(- Math.PI / 2),
            new MeshBasicMaterial()
        );

        this.reticle.matrixAutoUpdate = false;
        this.reticle.visible = false;

        this.scene.add(this.reticle);

        this.activeModelPlane = new Plane(new Vector3(0, 1, 0), 0);

        this.touches = [
			{
				x: 0,
				y: 0,
			},
			{
				x: 0,
				y: 0,
			},
		];

		this.reticleRotation = 0;

		this.startIntersect = new Vector3();
		this.endIntersect = new Vector3();
		this.haveStartPoint = false;

        this.raycaster = new Raycaster();

        // light estimation
		this.xrLight = new XREstimatedLight(renderer, true);

        this.defaultEnvironment = this.scene.environment;

		this.xrLight.addEventListener( 'estimationstart', this.onLightEstimationStart);
		this.xrLight.addEventListener( 'estimationEnd', this.onLightEstimationEnd);
    }

	static async isArSupported() {
		if(!('xr' in window.navigator))
			return false;

		return navigator.xr.isSessionSupported('immersive-ar');
	}

    update(timestamp, frame) {
        if(!this.session)
            return;

        if (frame) {
            const referenceSpace = this.renderer.xr.getReferenceSpace();

            if (hitTestSourceRequested === false) {

                this.session.requestReferenceSpace('viewer').then(referenceSpace => {

                    this.session.requestHitTestSource({ space: referenceSpace }).then(source => {
                        hitTestSource = source;
                    });

                });

                hitTestSourceRequested = true;
            }

            if (hitTestSource) {
                const hitTestResults = frame.getHitTestResults(hitTestSource);

                if (hitTestResults.length)
                    this.onHit(hitTestResults[0], referenceSpace);
                else
                    this.reticle.visible = false;
            }
        }

        this.renderer.render(this.scene, this.camera);
    }

    onHit(hit, referenceSpace) {
        if(this.activeScene?.visible)
            return;

        this.reticle.visible = true;
        this.reticle.matrix.fromArray(hit.getPose(referenceSpace).transform.matrix);
    }

    onTouchStart(e) {
        if (!this.activeScene?.visible && this.reticle.visible) {
            this.showActiveModel();

            this.reticle.visible = false;
        }

        for (let i = 0; i < e.touches.length && i < 2; ++i) {
            this.touches[i].x = e.touches[i].clientX;
            this.touches[i].y = e.touches[i].clientY;
        }

        // one touch to move
        if (e.touches.length === 1) {
            this.haveStartPoint = false;

            const xrCamera = this.renderer.xr.getCamera(this.camera);

            const result = this.rayPlaneTest(xrCamera, this.activeModelPlane, e.touches[0].clientX, e.touches[0].clientY,
                this.startIntersect);

            if (result) {
                this.haveStartPoint = true;
            }

        }
        // pinch to rotate
        else if (e.touches.length === 2) {
            vec2.set(e.touches[0].clientX - e.touches[1].clientX, e.touches[0].clientY - e.touches[1].clientY);

            this.startPinchlen = vec2.length();
        }
    }

    onTouchMove(e) {
        if (!this.activeScene || !this.activeScene.visible)
            return;

        // one touch to move
        if (e.touches.length === 1) {
            if (this.haveStartPoint) {
                const xrCamera = this.renderer.xr.getCamera(this.camera);

                const result = this.rayPlaneTest(xrCamera, this.activeModelPlane, e.touches[0].clientX, e.touches[0].clientY, this.endIntersect);
                if (result) {
                    vec3.subVectors(this.endIntersect, this.startIntersect);
                    if (vec3.length() < 0.2) {
                        if (this.activeScene)
                            this.activeScene.position.add(vec3);
                    }

                    this.startIntersect.copy(this.endIntersect);
                }
            }
        }
        // pinch to rotate
        else if (e.touches.length === 2) {
            vec2.set(e.touches[0].clientX - e.touches[1].clientX, e.touches[0].clientY - e.touches[1].clientY);

            const len = vec2.length();

            const diff = this.startPinchlen - len;

            this.startPinchlen = len;

            if (this.activeScene)
                this.activeScene.rotateY(diff * 0.01);
        }
    }

    onLightEstimationStart() {
        traverseTree(this.scene, o => {
            if(o instanceof Light)
                o.visible = false;
        });

        this.scene.add(this.xrLight);

        this.defaultEnvironment = this.scene.environment;

        if (this.xrLight.environment)
            this.scene.environment = this.xrLight.environment;
        else
            this.scene.environment = null;
    }
    
    onLightEstimationEnd() {
        this.scene.remove(this.xrLight);

        traverseTree(this.scene, o => {
            if(o instanceof Light) 
                o.visible = true;
        });

        this.scene.environment = this.defaultEnvironment;
    }

    async startSession() {
		this.session = await navigator.xr.requestSession( 'immersive-ar', { 
            requiredFeatures: [ 'local', 'hit-test'],
            optionalFeatures: [ 'dom-overlay', 'dom-overlay-for-handheld-ar', 'light-estimation' ],
            domOverlay: { root: this.domElem }       
        });

        this.renderer.xr.setReferenceSpaceType( 'local' );

		await this.renderer.xr.setSession( this.session );

        this.domElem.addEventListener('touchstart', this.onTouchStart);
        this.domElem.addEventListener('touchmove', this.onTouchMove);
       
        this.sessionStartHandler && this.sessionStartHandler();
    }

    endSession() {
        hitTestSourceRequested = false;
        hitTestSource = null;

        this.session.end();

        this.session = null;

        this.domElem.removeEventListener('touchstart', this.onTouchStart);
        this.domElem.removeEventListener('touchmove', this.onTouchMove);

        this.reticle.visible = false;

        this.activeScene.removeFromParent();

        if(this.sceneParent)
            this.sceneParent.add(this.activeScene);

        this.activeScene.visible = true;

        this.sessionEndHandler && this.sessionEndHandler();
    }

    setActiveModel(model) {
        this.activeScene = model;

        if(model.parent) {
            this.sceneParent = model.parent;

            model.removeFromParent();
        }

        this.scene.add(model);

        this.activeScene.visible = false;
    }

    showActiveModel() {
        if (!this.activeScene)
            return;

        // position model and plane on reticle
        mtx4.extractRotation(this.reticle.matrix);

        this.activeScene.setRotationFromMatrix(mtx4);
        this.activeScene.position.setFromMatrixPosition(this.reticle.matrix);

        this.activeModelPlane.set(Vector3_YAxis, 0);
        this.activeModelPlane.applyMatrix4(this.reticle.matrix);

        boundingBox.setFromObject(this.activeScene);

        const yOffset = this.activeScene.position.y - boundingBox.min.y;

        this.activeScene.position.y += yOffset;

        this.activeScene.visible = true;
    }
    
	rayPlaneTest(camera, plane, tx, ty, result){
		vec2.x = (tx / window.innerWidth ) * 2 - 1;
		vec2.y = - (ty / window.innerHeight ) * 2 + 1;
		
		this.raycaster.setFromCamera( vec2, camera );			
	
		return this.raycaster.ray.intersectPlane(plane, result);
	}
}