import { InvalidArgumentError } from '../../core/error/exceptions';
import {
    Vector3,
    Quaternion,
} from '../../three/build/three';

export default class CameraAnimation {
    constructor(cameraController) {
        if(!cameraController)
            throw new InvalidArgumentError('cameraController');

        this.cameraController = cameraController;
        this.camera = cameraController.object;
        this.targetQuaternion = new Quaternion();
        this.targetPosition = new Vector3();
        this.startPosition = new Vector3();
        this.lookAtPoint = new Vector3();
        this.startQuaternion = new Quaternion();
    }

    start(targetPosition, targetRotation, cameraState, onCompleted) {
        this.rotSpeed = 0.75;
        this.transSpeed = 0.75;
        this.animating = true;
        this.startQuaternion.copy(this.camera.quaternion);
        this.targetQuaternion.copy(targetRotation);
        this.targetPosition.copy(targetPosition);
        this.startPosition.copy(this.camera.position);
        this.positionAlpha = 0;
        this.rotationAlpha = 0;
        this.cameraState = cameraState;
        this.onCompleted = onCompleted;
    }

    update(delta) {
        if(!this.animating)
            return;
     
        this.rotationAlpha += this.rotSpeed * delta;
        if(this.rotationAlpha >= 1)
            this.rotationAlpha = 1;
            
        this.camera.quaternion.slerpQuaternions(this.startQuaternion, this.targetQuaternion, this.rotationAlpha);
    
        this.positionAlpha += this.transSpeed * delta;
        if(this.positionAlpha >= 1)
            this.positionAlpha = 1;

        this.camera.position.lerpVectors(this.startPosition, this.targetPosition, this.positionAlpha);

        if(this.rotationAlpha === 1 && this.positionAlpha === 1) {
            this.animating = false;
            this.cameraController.setState(this.cameraState);

            this.onCompleted && this.onCompleted();
        }
    }
}