import { InvalidArgumentError } from '../../core/error/exceptions';
import {
    Vector3,
    Box3,
    Sphere,
    Object3D
} from '../../three/build/three';

export default class BoundingGeometry {
    constructor(model) {
        this._center = new Vector3();
        this._size = new Vector3();
        this._bSphere = new Sphere();
        this._bBox = new Box3();

        this.setFromModel(model);
    }

    setFromModel(model) {
        if(!model || !(model instanceof Object3D))
            throw new InvalidArgumentError('model');

        this._bBox.setFromObject(model);
        this._bBox.getCenter(this._center);
        this._bBox.getSize(this._size);
        this._bBox.getBoundingSphere(this._bSphere);
    }

    get boundingBox() { return this._bBox; }
    get boundingSphere() { return this._bSphere; }
    get size() { return this._size; }
    get center() { return this._center; }
}