import { AnimationClip } from "./ezyvuecore/three/build/three";

export function deserializeAnimations(scene) {
    scene.traverse(object => {
        if (object.userData.animations) {
            object.animations = [];

            object.userData.animations.forEach(json => {
                object.animations.push(AnimationClip.parse(json));
            });
        }
    });
}
