import GltfLoader from "./ezyvuecore/engine/three/gltfLoader";
import { loadHdrTextureFromBlob } from "./textureFile";
import { EquirectangularReflectionMapping, Object3D } from "./ezyvuecore/three/build/three";
import { readFileAsText } from "./file";
import { loadZipFile, loadZipFileFromUrl } from "./loadFromZip";
import EditorSettings from "./editorSettings";
import { deserializeAnimations } from "./animation";

export default class ProjectImporter {
 
    getSceneRoot(object) {
        if (!object.name.startsWith('AuxScene')) {
            return object;
        }

        if (object.children.length == 1) {
            return this.getSceneRoot(object.children[0]);
        }

        return object;
    }

    async parseFile(files, renderer, onLoad, onError) {
        let settings;
        let scene;
        let loadingImage;
        let envMap;

        for (const key in files) {
            if (Object.hasOwnProperty.call(files, key)) {
                const file = files[key];

                if (key === 'scene.glb') {
                    const loader = new GltfLoader(renderer);
                    const gltf = await loader.parse(file.buffer, onError);

                    scene = gltf.scene;

                    // skip AuxScene added by exporter
                    while (scene.name.startsWith('AuxScene') && scene.children.length === 1)
                        scene = scene.children[0];

                    scene.parent && scene.removeFromParent();

                    deserializeAnimations(scene);
                }
                else if (key === 'envmap.hdr') {
                    const blob = new Blob([file.buffer]);
                    const tex = await loadHdrTextureFromBlob(blob);

                    tex.mapping = EquirectangularReflectionMapping;

                    envMap = tex;
                }
                else if (key === 'loading.jpg') {
                    const blob = new Blob([file.buffer]);
                    loadingImage = URL.createObjectURL(blob);
                }
                else if (key === 'settings.json') {
                    const blob = new Blob([file.buffer]);
                    const json = await readFileAsText(blob);

                    settings = new EditorSettings();
                    settings.state = JSON.parse(json);
                }
            }
        }

        if (!scene)
            scene = new Object3D();

        if (!settings)
            settings = new EditorSettings();

        onLoad({
            scene: scene,
            settings: settings,
            envMap: envMap,
            loadingImage: loadingImage
        });
    }

    async load(file, renderer, onLoad, onProgress, onError) {
        try {
            if(!file)
                throw new Error('no file or path');
            
            if (typeof file === 'string')
                loadZipFileFromUrl(file, async files => this.parseFile(files, renderer, onLoad, onError), onProgress, onError);
            else
                loadZipFile(file, async files => this.parseFile(files, renderer, onLoad, onError));
        }
        catch (e) {
            onError && onError('An error occured loading the project file: ' + e.message);
        }
    }
}