let id = 1;

const closeImg = 'data:image/png;base64,';

export default class InfoWindow {
    constructor(title, desc) {
        this.elem = document.createElement('div');
        this.elem.className = 'ezv-info-window ezv-center-both';
        this.elem.style.display = 'none';

        const butId = 'ezv-info-close-' + id++;

        this.elem.innerHTML = `
            <div class='ezv-info-header'>
                ${title}
                <img id=${butId} src=${closeImg} style='width: 24px; vertical-align: middle;}} alt='' />
            </div>
            <div class='ezv-info-body'>
                <p>${desc}</p>
            </div>
        </div>`;

        this.elem.querySelector(`#${butId}`).addEventListener('pointerdown', () => this.elem.style.display = 'none');
    }
}