import React from "react"
import PropTypes from 'prop-types';
import { createViewer } from "./bundle";

export default class Viewer extends React.Component {
    constructor() {
        super();

        this.elemRef = React.createRef();
    }
    
    componentDidMount() {
        createViewer(this.elemRef.current);
    }

    render() {
        return (
            <div ref={this.elemRef} style={this.props.style} width="100%" height="100%" 
                project-file={this.props.projectFile} 
                glb-file={this.props.glbFile} >    
            </div>
        )
    }
}

Viewer.propTypes={
    style: PropTypes.object,
    projectFile: PropTypes.string,
    glbFile: PropTypes.string
}