import { SRGBColorSpace, Texture } from "./ezyvuecore/three/build/three";
import { RGBELoader } from "./ezyvuecore/three/examples/jsm/loaders/RGBELoader";
import { TGALoader } from "./ezyvuecore/three/examples/jsm/loaders/TGALoader";
import { readFileAsDataUrl } from "./file";

export async function loadHdrTextureFromDataUrl(url) {
    return new Promise((resolve, reject) => {
        const loader = new RGBELoader();
        loader.load(url, hdrTexture => {
            hdrTexture.isHDRTexture = true;
            hdrTexture.dataUrl = url;
            hdrTexture.needsUpdate = true;

            resolve(hdrTexture);
        },
        undefined,
        e => reject(`failed to load hdr ${e}`));
    });
}

export async function loadTgaTextureFromDataUrl(url) {
    return new Promise((resolve, reject) => {
        const loader = new TGALoader();
        loader.load(url, texture => {
            texture.colorSpace = SRGBColorSpace;
            texture.dataUrl = url;
            texture.needsUpdate = true;

            resolve(texture);
        },
        undefined,
        e => reject(`failed to load tga ${e}`));
    });
}

export async function loadTextureFromDataUrl(url) {
    return new Promise((resolve) => {
        const image = document.createElement('img');
        image.addEventListener('load', function () {
            const texture = new Texture(this);
            texture.dataUrl = url;
            texture.needsUpdate = true;

            resolve(texture);
        }, false);

        image.src = url;
    });
}

export async function loadHdrTextureFromBlob(blob) {
    const url = await readFileAsDataUrl(blob);
    return loadHdrTextureFromDataUrl(url);
}

export async function loadTexture(file) {
    const extension = file.name.split('.').pop().toLowerCase();
    if (extension === 'hdr' || extension === 'pic') {
        const url = await readFileAsDataUrl(file);
        const tex = await loadHdrTextureFromDataUrl(url);
        return tex;
    }
    else if (extension === 'tga') {
        const dataUrl = await readFileAsDataUrl(file);
        const tex = await loadTgaTextureFromDataUrl(dataUrl);
        return tex;
    }
    else if (file.type.match('image.*')) {
        const dataUrl = await readFileAsDataUrl(file);
        const tex = await loadTextureFromDataUrl(dataUrl);
        return tex;
    }
}
