import { FileLoader } from './ezyvuecore/three/build/three';
import { unzipSync } from 'fflate';

export async function loadZipFileFromUrl(url, onLoad, onProgress, onError) {
    const loader = new FileLoader();
    loader.setResponseType('arraybuffer');

    loader.load(url, buf => {
        const u8 = new Uint8Array(buf);
        const files = unzipSync(u8);

        onLoad(files);
    },
    xhr => {
        onProgress && onProgress((xhr.loaded / xhr.total * 100))
    },
    onError);
}

export async function loadZipFile(file, onLoad) {
    const reader = new FileReader();

    reader.addEventListener('load', async event => {
        const files = unzipSync(new Uint8Array(event.target.result));

        onLoad(files);
    });

    reader.readAsArrayBuffer(file);
}
