
export class UICommands {
    constructor(viewer) {

        this.viewer = viewer;
    }

    execute(name, args) {
        switch(name) {
            case 'set-auto-rotate':
                this.viewer.orbitCameraController.autoRotate = args.arg1;
                break;
            case 'reset-camera':
                this.viewer.frameScene();
                break;
            case 'toggle-fullscreen':
                this.viewer.toggleFullScreen();
                break;
            case 'toggle-measuretool':
                this.viewer.measureTool.object.visible = !this.viewer.measureTool.object.visible;
                break;
        }
    }
}
