import { InvalidArgumentError } from '../../core/error/exceptions';
import {
    AnimationMixer,
    LoopRepeat,
    Object3D,
} from '../../three/build/three';

export default class AnimationPlayer {
    constructor(rootNode, clips) {
        if(!rootNode || !(rootNode instanceof Object3D))
            throw new InvalidArgumentError('rootNode');

        if(!clips)
            throw new InvalidArgumentError('clips');

        this._clips = clips;
        this._mixer = new AnimationMixer(rootNode);

        this._mixer.addEventListener('finished', e => {
            this.onClipEndEventHandler && this.onClipEndEventHandler(e.action);

            e.action.userData.finished = true;
            e.action.userData.openState = e.action.userData.openState ? 0 : 1;
        });
    }

    get clips() {return this._clips;}
    get mixer() {return this._mixer;}

    update(delta) {
        if(this._mixer)
            this._mixer.update(delta);
    }

    playClip(clip, loop = false, openClose = false, fadeTime = 0) { 
        const clipAction = this._mixer.clipAction(clip);
        if(!clipAction.userData)
            clipAction.userData = {};

        if(clipAction.isRunning() && clipAction.repetitions !==  Number.POSITIVE_INFINITY)
            return;
            
        if(clipAction.userData.openState === undefined)
            clipAction.userData.openState = 0;

        if(loop)
            openClose = false;

        if(!openClose || loop)
            clipAction.userData.openState = 0;

        clipAction.setLoop(LoopRepeat, loop ? Number.POSITIVE_INFINITY : 1);

        if(clipAction.userData.finished && openClose)
            clipAction.timeScale = clipAction.userData.openState ? -1 : 1;
        
        clipAction.clampWhenFinished = true;
        clipAction.enable = true;

        if(this.currentAction && this.currentAction !== clipAction)
            this.currentAction.fadeOut(fadeTime);
        else
            fadeTime = 0;

        clipAction.reset().fadeIn(fadeTime).play();

        clipAction.finished = false;

        this.currentAction = clipAction;
    }

    getClipByName(name) {
        return this._clips.find(clip => clip.name === name);
    }

    stopClip(clip) {
        if(!clip)
            throw new InvalidArgumentError('clip');

        const clipAction = this._mixer.clipAction(clip);

        clipAction.stop();
    }

    stopAll() {
        this._clips.forEach( clip => {
            this._mixer.clipAction( clip ).stop();
        } );
    }

    pauseClip(clip) {
        if(!clip)
            throw new InvalidArgumentError('clip');

        const clipAction = this._mixer.clipAction(clip);

        if(clipAction.isRunning)
            clipAction.paused = true;
    }

    unPauseClip(clip) {
        if(!clip)
            throw new InvalidArgumentError('clip');
        
        const clipAction = this._mixer.clipAction(clip);
            
        clipAction.paused = false;
    }


    isClipPlaying(clip) {
        if(!clip)
            throw new InvalidArgumentError('clip');

        const clipAction = this._mixer.clipAction(clip);
        
        return clipAction.isRunning();
    }

    isClipPaused(clip) {
        if(!clip)
            throw new InvalidArgumentError('clip');

        const clipAction = this._mixer.clipAction(clip);
        
        return clipAction.paused;
    }

    isAnyClipPlaying() {
        this._clips.forEach( clip => {
            if(this._mixer.clipAction( clip ).isRunning)
                return true;
        } );

        return false;
    }
}