import {
	Euler,
	MathUtils,
	Spherical,
	Vector3
} from '../../three/build/three';

const _lookDirection = new Vector3();
const _spherical = new Spherical();
const _target = new Vector3();

class FirstPersonControls {

	constructor(object, domElement) {

		this.object = object;

		this.domElement = domElement;

		this.enabled = true;

		this.movementSpeed = 1.0;
		this.lookSpeed = 0.005;

		this.lookVertical = true;
		this.autoForward = false;

		this.activeLook = true;

		this.heightSpeed = false;
		this.heightCoef = 1.0;
		this.heightMin = 0.0;
		this.heightMax = 1.0;

		this.constrainVertical = false;
		this.verticalMin = 0;
		this.verticalMax = Math.PI;

		this.mouseDragOn = false;

		this.autoSpeedFactor = 0.0;

		this.pointerX = 0;
		this.pointerY = 0;
		this.pointerDeltaX = 0;
		this.pointerDeltaY = 0;

		this.moveForward = false;
		this.moveBackward = false;
		this.moveLeft = false;
		this.moveRight = false;

		this.virtualJoystickDx = 0;
		this.virtualJoystickDy = 0;

		this.viewHalfX = 0;
		this.viewHalfY = 0;

		// private variables

		this.lat = 0;
		this.lon = 0;

		this.targetPosition = new Vector3();

		this.onKeyDown = this.onKeyDown.bind(this);
		this.onKeyUp = this.onKeyUp.bind(this);
		this.onPointerDown = this.onPointerDown.bind(this);
		this.onPointerUp = this.onPointerUp.bind(this);
		this.onPointerMove = this.onPointerMove.bind(this);


		this.domElement.addEventListener('pointerdown', this.onPointerDown);

		window.addEventListener('keydown', this.onKeyDown);
		window.addEventListener('keyup', this.onKeyUp);
		
		this.handleResize();

		this.setOrientation(this);
	}


	dispose() {

		this.domElement.removeEventListener('pointerup', this.onPointerUp);

		window.removeEventListener('keydown', this.onKeyDown);
		window.removeEventListener('keyup', this.onKeyUp);

	}

	onPointerDown(event) {
		if (event.isPrimary === false)
			return;

		this.pointerX = event.clientX;
		this.pointerY = event.clientY;

		this.domElement.addEventListener('pointermove', this.onPointerMove);
		this.domElement.addEventListener('pointerup', this.onPointerUp);
	}

	onPointerMove(event) {
		if (event.isPrimary === false)
			return;

		this.pointerDeltaX = (this.pointerX - event.clientX);
		this.pointerDeltaY = (this.pointerY - event.clientY);

		this.pointerX = event.clientX;
		this.pointerY = event.clientY;

		this.lon -= this.pointerDeltaX * this.lookSpeed;
		this.lat -= this.pointerDeltaY * this.lookSpeed;

		this.lat = Math.max(- 85, Math.min(85, this.lat));
	}

	onPointerUp(event) {
		if (event.isPrimary === false)
			return;

		this.pointerDeltaX = 0;
		this.pointerDeltaY = 0;

		this.domElement.removeEventListener('pointermove', this.onPointerMove);
		this.domElement.removeEventListener('pointerup', this.onPointerUp);
	}

	setOrientation(controls) {
		const quaternion = controls.object.quaternion;

		_lookDirection.set(0, 0, - 1).applyQuaternion(quaternion);
		_spherical.setFromVector3(_lookDirection);

		this.lat = 90 - MathUtils.radToDeg(_spherical.phi);
		this.lon = MathUtils.radToDeg(_spherical.theta);
	}

	getState() {
		return {
			matrix: this.object.matrix.clone(),
			controllerType: 'firstperson'
		}
	}

	setState(state) {
		this.object.quaternion.setFromRotationMatrix(state.matrix);
		this.object.position.setFromMatrixPosition(state.matrix);

		this.setOrientation(this);
	}

	handleResize() {
		if (this.domElement === document) {

			this.viewHalfX = window.innerWidth / 2;
			this.viewHalfY = window.innerHeight / 2;

		} else {

			this.viewHalfX = this.domElement.offsetWidth / 2;
			this.viewHalfY = this.domElement.offsetHeight / 2;

		}
	}

	onKeyDown(event) {
		switch (event.code) {

			case 'ArrowUp':
			case 'KeyW': this.moveForward = true; break;

			case 'ArrowLeft':
			case 'KeyA': this.moveLeft = true; break;

			case 'ArrowDown':
			case 'KeyS': this.moveBackward = true; break;

			case 'ArrowRight':
			case 'KeyD': this.moveRight = true; break;

			case 'KeyR': this.moveUp = true; break;
			case 'KeyF': this.moveDown = true; break;
		}
	}

	onKeyUp(event) {
		switch (event.code) {

			case 'ArrowUp':
			case 'KeyW': this.moveForward = false; break;

			case 'ArrowLeft':
			case 'KeyA': this.moveLeft = false; break;

			case 'ArrowDown':
			case 'KeyS': this.moveBackward = false; break;

			case 'ArrowRight':
			case 'KeyD': this.moveRight = false; break;

			case 'KeyR': this.moveUp = false; break;
			case 'KeyF': this.moveDown = false; break;
		}
	}

	lookAt(x, y, z) {
		if (x.isVector3) {
			_target.copy(x);

		} else {
			_target.set(x, y, z);
		}

		this.object.lookAt(_target);

		this.setOrientation(this);
		
		return this;
	}

	update(delta) {
		if (this.enabled === false) return;

		const camY = this.object.position.y;

		if (this.heightSpeed) {

			const y = MathUtils.clamp(this.object.position.y, this.heightMin, this.heightMax);
			const heightDelta = y - this.heightMin;

			this.autoSpeedFactor = delta * (heightDelta * this.heightCoef);

		} else {

			this.autoSpeedFactor = 0.0;

		}

		const actualMoveSpeed = delta * this.movementSpeed;

		if (this.moveForward || (this.autoForward && !this.moveBackward)) this.object.translateZ(- (actualMoveSpeed + this.autoSpeedFactor));
		if (this.moveBackward) this.object.translateZ(actualMoveSpeed);

		if (this.moveLeft) this.object.translateX(- actualMoveSpeed);
		if (this.moveRight) this.object.translateX(actualMoveSpeed);

		this.object.translateX(actualMoveSpeed  * 0.1 * this.virtualJoystickDx);
		this.object.translateZ(actualMoveSpeed  * 0.1 * this.virtualJoystickDy);

		if (this.moveUp) this.object.translateY(actualMoveSpeed);
		if (this.moveDown) this.object.translateY(- actualMoveSpeed);


		let phi = MathUtils.degToRad(90 - this.lat);
		const theta = MathUtils.degToRad(this.lon);

		if (this.constrainVertical) {

			phi = MathUtils.mapLinear(phi, 0, Math.PI, this.verticalMin, this.verticalMax);
		}

		const position = this.object.position;

		this.targetPosition.setFromSphericalCoords(1, phi, theta).add(position);

		this.object.lookAt(this.targetPosition);

		if(!this.moveUp && !this.moveDown)
			this.object.position.y  = camY;
	}

	onVirtualJoyStickMove(x, y) {
		this.virtualJoystickDx = x;
		this.virtualJoystickDy = y;
	}

	onVirtualJoyStickTouch() {
	}

	onVirtualJoyStickRelease() {
		this.virtualJoystickDx = 0;
		this.virtualJoystickDy = 0;
	}
}

export { FirstPersonControls };
