import {
    MeshBasicMaterial,
    CanvasTexture,
    LinearFilter,
    ClampToEdgeWrapping,
    Vector3,
    BufferGeometry,
    LineBasicMaterial,
    Line,
    Mesh,
    PlaneGeometry,
    DoubleSide,
    Object3D,
    Euler,
    RGBAFormat
} from './ezyvuecore/three/build/three';
import BoundingGeometry from './ezyvuecore/engine/three/boundingGeometry';

function createLabelTexture(text) {
    const ctx = document.createElement('canvas').getContext('2d');
    ctx.canvas.width = 128;
    ctx.canvas.height = 64;

    const x = 64;
    const y = 32;

    ctx.fillStyle = "#000000";
    ctx.font = "32px sans-serif";
    ctx.textAlign = "center";
    ctx.textBaseline = "middle";

    ctx.fillText(text, x, y);

    return new CanvasTexture(ctx.canvas); 
}

function createAxisLabel(text, width, height) {
    const texture = createLabelTexture(text);

    texture.minFilter = LinearFilter;
    texture.wrapS = ClampToEdgeWrapping;
    texture.wrapT = ClampToEdgeWrapping;

    var geometry = new PlaneGeometry( width, height);
    
    var material = new MeshBasicMaterial({side: DoubleSide});
    material.map = texture;
    material.depthWrite = false;
    material.transparent = true;
    material.format = RGBAFormat;

    var plane = new Mesh( geometry, material );

    return plane;
}

function getLabelText(value)
{
    let text = '';
    if(value < 1)
        text = (value * 100).toFixed(2) + ' cm'
    else
        text = value.toFixed(2) + ' m';

    return text;
}

function addZAxis(axes, box, color) {
    const points = [];

    const min = box.min;
    const max = box.max;

    const size = new Vector3();
    box.getSize(size);

    let maxLen = Math.max(size.x, size.y);
    maxLen = Math.max(size.y, size.z);

    const offset = maxLen * 0.05;
    
    // z axis
    points.push(new Vector3(min.x, min.y, min.z));
    points.push(new Vector3(min.x-offset, min.y, min.z));

    points.push(new Vector3(min.x-offset, min.y, min.z));
    points.push(new Vector3(min.x-offset, min.y, max.z));

    points.push(new Vector3(min.x-offset, min.y, max.z));
    points.push(new Vector3(min.x, min.y, max.z));

    const geometry = new BufferGeometry().setFromPoints(points);

    const material = new LineBasicMaterial({
        color: color,
    });

    const lines = new Line(geometry, material);

    axes.add(lines);

    const label = createAxisLabel(getLabelText(size.z), offset * 4, offset * 2);

    label.rotateX(-Math.PI/2.0);
    label.rotateZ(-Math.PI/2.0);

    label.position.set(min.x-offset*2, min.y, min.z + (max.z-min.z)/2);

    axes.add(label);
}

function addXAxis(axes, box, color) {
    const points = [];

    const min = box.min;
    const max = box.max;

    const size = new Vector3();
    box.getSize(size);

    let maxLen = Math.max(size.x, size.y);
    maxLen = Math.max(size.y, size.z);

    const offset = maxLen * 0.05;
    
    // z axis
    points.push(new Vector3(min.x, min.y, min.z));
    points.push(new Vector3(min.x, min.y, min.z-offset));

    points.push(new Vector3(min.x, min.y, min.z-offset));
    points.push(new Vector3(max.x, min.y, min.z-offset));
  
    points.push(new Vector3(max.x, min.y, min.z-offset));
    points.push(new Vector3(max.x, min.y, min.z));

    const geometry = new BufferGeometry().setFromPoints(points);

    const material = new LineBasicMaterial({
        color: color
    });

    const lines = new Line(geometry, material);

    axes.add(lines);

    const label = createAxisLabel(getLabelText(size.x), offset * 4, offset * 2);

    label.rotateX(-Math.PI/2.0);
    label.rotateZ(Math.PI);

    label.position.set(min.x + (max.x-min.x)/2, min.y, min.z-offset*2);

    axes.add(label);
}

function addYAxis(axes, box, color) {
    const points = [];

    const min = box.min;
    const max = box.max;

    const size = new Vector3();
    box.getSize(size);

    let maxLen = Math.max(size.x, size.y);
    maxLen = Math.max(size.y, size.z);

    const offset = maxLen * 0.05;
    
    points.push(new Vector3(min.x, min.y, min.z));
    points.push(new Vector3(min.x-offset, min.y, min.z));

    points.push(new Vector3(min.x-offset, min.y, min.z));
    points.push(new Vector3(min.x-offset, max.y, min.z));
  
    points.push(new Vector3(min.x-offset, max.y, min.z));
    points.push(new Vector3(min.x, max.y, min.z));

    const geometry = new BufferGeometry().setFromPoints(points);

    const material = new LineBasicMaterial({
        color: color
    });

    const lines = new Line(geometry, material);

    axes.add(lines);

    const label = createAxisLabel(getLabelText(size.y), offset * 4, offset * 2);

//    label.rotateY(-Math.PI/2.0);
//    label.rotateX(-Math.PI/2.0);
     label.rotateZ(Math.PI/2);

    label.position.set(min.x-offset*2, min.y + (max.y-min.y)/2, min.z);

    axes.add(label);
}

export default class MeasureTool 
{
    constructor(scene) {
        const pos = new Vector3(scene.position.x, scene.position.y, scene.position.z);
        const scale = new Vector3(scene.scale.x, scene.scale.y, scene.scale.z);
        const rot = new Euler( scene.rotation.x,  scene.rotation.y,  scene.rotation.z);

        scene.position.set(0, 0, 0);
        scene.scale.set(1, 1, 1);
        scene.rotation.set(0, 0, 0);

        const boundingGeometry = new BoundingGeometry(scene);
        
        this._object = new Object3D();

        const color = 0;

        addZAxis(this._object, boundingGeometry.boundingBox, color);
        addYAxis(this._object, boundingGeometry.boundingBox, color);
        addXAxis(this._object, boundingGeometry.boundingBox, color);

        scene.position.copy(pos);
        scene.scale.copy(scale);
        scene.rotation.copy(rot);
    }

    get object() {
        return this._object;
    }
}