let id = 1;

const closeImg = 'data:image/png;base64,';

export default class HelpWindow {
    constructor() {
        this.elem = document.createElement('div');
        this.elem.className = 'ezv-help-window';
        this.elem.style.display = 'none';

        const bodyId = 'ezv-help-' + id++;
        const butId = 'ezv-help-close-' + id++;

        this.elem.innerHTML = `
                <div class='ezv-help-window-header'>
                    <div id=${butId} style='margin-left: auto'>
                        <img src=${closeImg} style='width: 24px; vertical-align: middle;' alt='' />
                    </div>
                </div>
                <div id=${bodyId}>
            </div>`;

        this.elem.querySelector(`#${bodyId}`).innerHTML = `
            <div>
            <img src='./assets/images/rotate.png' style='width: 24px; vertical-align: middle;' alt='' />&nbsp;Orbit Camera<br /><br />
            Left mouse and move / drag one finger<br /><br />
            <img src='./assets/images/pan.png' style='width: 24px; vertical-align: middle;' alt='' />&nbsp;Orbit Camera<br /><br />
            Left mouse, CTRL key and move / drag two fingers<br /><br />
            <img src='./assets/images/zoomout.png' style='width: 24px; vertical-align: middle;' alt='' />&nbsp;Orbit Camera<br /><br />
            Mouse Wheel / pinch<br /><br />`;

        this.elem.querySelector(`#${butId}`).addEventListener('pointerdown', () => this.elem.style.display = 'none');
    }
}