import {
    MeshBasicMaterial,
    PlaneGeometry,
    Mesh,
    WebGLRenderTarget,
    RGBAFormat,
    Object3D,
} from '../../three/build/three';

import AoBakedShadowRenderer from './aoBakedShadowRenderer';
import BoundingGeometry from './boundingGeometry';
import Renderer from './renderer';

const BAKED_SHADOW_RENDER_ORDER = 1000000;

export default class BakedShadowPlane {
    constructor(renderer, model, bakedShadowLightness = 1, bakedShadowBlur = 0.5, bakedShadowFalloff = 0) {
        if(!renderer || !(renderer instanceof Renderer))
            throw 'renderer argument invalid';

        if(!model || !(model instanceof Object3D))
            throw 'model argument invalid';

        this._boundingGeometry = new BoundingGeometry(model);

        const xAdjust = this._boundingGeometry.size.x * 0.2 * 2.0;
        const zAdjust = this._boundingGeometry.size.z * 0.2 * 2.0;

        this._renderTarget = new WebGLRenderTarget(512, 512, { format: RGBAFormat });

        const material = new MeshBasicMaterial();

        material.map = this._renderTarget.texture;
        material.depthWrite = false;
        material.transparent = true;
        material.opacity = 1 - bakedShadowLightness;

        material.needsUpdate = true;

        this._mesh = new Mesh(new PlaneGeometry(this._boundingGeometry.size.x + xAdjust,
            this._boundingGeometry.size.z + zAdjust, 1, 1), material);

        this._mesh.rotation.x = Math.PI * -.5;
        this._mesh.renderOrder = BAKED_SHADOW_RENDER_ORDER;

        this._mesh.name = '_BAKED_SHADOW_MESH';

        this._render(renderer, model, bakedShadowLightness, bakedShadowBlur, bakedShadowFalloff);
    }

    _render(renderer, model, bakedShadowLightness = 1.0, bakedShadowBlur = 0.5, bakedShadowFalloff = 0) {
        this._mesh.material.opacity = 1 - bakedShadowLightness;

        this._mesh.material.needsUpdate = true;
        
        AoBakedShadowRenderer.renderShadow(renderer, this._renderTarget, this._mesh, model, bakedShadowBlur, bakedShadowFalloff);
    }

    dispose() {
        this._mesh.material.dispose();
        this._mesh.geometry.dispose();
        this._renderTarget.dispose();
    }

    get mesh() { return this._mesh; }
    get renderTarget() { return this._renderTarget; }
}