//import {BlurPass, SavePass, EffectComposer, RenderPass} from 'postprocessing';
import {
    Vector2,
    Vector3,
    Box3,
    OrthographicCamera,
    Sphere,
    ShaderMaterial,
    GreaterDepth,
    BackSide,
    Mesh,
    Scene,
    Color,
    PlaneGeometry,
} from '../../three/build/three';


import { RenderPass } from '../../three/examples/jsm/postprocessing/RenderPass.js';
import { ConvolutionShader } from '../../three/examples/jsm/shaders/ConvolutionShader.js';
import { ShaderPass } from '../../three/examples/jsm/postprocessing/ShaderPass.js';
import { EffectComposer } from '../../three/examples/jsm/postprocessing/EffectComposer.js';
import ResourceTracker from './resourceTracker';
import { SavePass } from '../../three/examples/jsm/postprocessing/SavePass.js';

const vec2 = new Vector2();
const vec = new Vector3();

var shader = {

	uniforms: {
    },

	vertexShader: [
        "varying vec4 vUv;",
		"void main() {",
        "   vec4 pos = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );",
		"   gl_Position = pos;",
        "   vUv = pos;",
		"}"

	].join( "\n" ),

	fragmentShader: [
        "varying vec4 vUv;",
		"void main() {",
		"	gl_FragColor = vec4( 0, 0, 0, ((vUv.z / vUv.w) + 1.0) / 2.0);",
		"}"

	].join( "\n" )
};

export default class AoBakedShadowRenderer
{
    static renderShadow(renderer, renderTarget, bakedShadowPlane, model, softness, falloff) {
        const tracker = new ResourceTracker();

        renderer.renderer.getSize(vec2);

        // get model bounds
        const modelBB = new Box3().setFromObject(model);

        modelBB.getSize(vec);

        const center = new Vector3();
        modelBB.getCenter(center);

        const modelBS = new Sphere();
        modelBB.getBoundingSphere(modelBS);

        const xmargin = vec.x * 0.2;
        const zmargin = vec.z * 0.2;

        // create and position a camera above the model
        const shadowCam = new OrthographicCamera( -(vec.x/2 + xmargin), 
                                                        vec.x/2 + xmargin,
                                                        vec.z/2 + zmargin, 
                                                        -(vec.z/2 + zmargin),
                                                        vec.y * falloff + vec.y * 0.001,
                                                        vec.y + vec.y * 0.1);
        shadowCam.position.add(center);
        shadowCam.position.y = modelBB.max.y + vec.y * 0.05;

        const lookAt = shadowCam.position.clone();
        lookAt.y = modelBB.min.y;

        shadowCam.lookAt(lookAt);

        // create shader
        const material = new ShaderMaterial( {
			fragmentShader: shader.fragmentShader,
			vertexShader: shader.vertexShader,
		} );

        // render furthest back-facing tris from near plane
        material.depthFunc = GreaterDepth;        
        material.side = BackSide;                 

        renderer.renderer.state.buffers.depth.setClear(0);

        // appply shader material to a cloned model
        const clonedModel = model.clone();
        clonedModel.traverse(object => {
            if (object instanceof Mesh) {
                object.material = material;
            }
        });

        // create scene
        const shadowScene = new Scene();

        shadowScene.add(clonedModel);
        shadowScene.add(shadowCam);

        shadowScene.background = null;

        // set up composer and passes
        const res = 512;

        const shadowComposer = new EffectComposer(renderer.renderer);
        shadowComposer.setSize(res, res);

        const shadowRenderScenePass = new RenderPass(shadowScene, shadowCam);
        shadowComposer.addPass(shadowRenderScenePass);

        const sigma = 4;

        // add convolution blur passes
        var hBlurShader = {...ConvolutionShader};
    
        hBlurShader.uniforms[ "uImageIncrement" ].value =  new Vector2( 0.001953125, 0.0 );  // 1/512
        hBlurShader.uniforms[ "cKernel" ].value = ConvolutionShader.buildKernel( sigma );
        hBlurShader.uniforms[ "tDiffuse" ].value = renderTarget.texture;

		const hBlurPass = new ShaderPass(hBlurShader);

        var vBlurShader = {...ConvolutionShader};
    
        vBlurShader.uniforms[ "uImageIncrement" ].value =  new Vector2( 0, 0.001953125 );
        vBlurShader.uniforms[ "cKernel" ].value = ConvolutionShader.buildKernel( sigma );
        vBlurShader.uniforms[ "tDiffuse" ].value = renderTarget.texture;

		const vBlurPass = new ShaderPass(vBlurShader);
      
        for(let i = 1; i < (softness) * 8; ++i) {
            shadowComposer.addPass(hBlurPass);
            shadowComposer.addPass(vBlurPass);
        }

		shadowComposer.addPass(new SavePass(renderTarget));

        tracker.track(shadowScene);

        bakedShadowPlane.position.set(center.x, 0, center.z);

        const xAdjust = vec.x * 0.2 * 2.0;
        const zAdjust = vec.z * 0.2 * 2.0;

        bakedShadowPlane.geometry.dispose();

        bakedShadowPlane.geometry = new PlaneGeometry(vec.x + xAdjust, vec.z + zAdjust, 1, 1)

        // render to texture
        renderer.renderer.setRenderTarget(renderTarget);

        const clearColor = new Color();
        renderer.renderer.getClearColor(clearColor);

        const clearAlpha = renderer.renderer.getClearAlpha();

        renderer.renderer.setClearColor (new Color(0), 0 );

        shadowComposer.render();

        // restore state
        renderer.renderer.setClearColor (clearColor, clearAlpha);

        renderer.renderer.setRenderTarget(null);

        renderer.renderer.state.buffers.depth.setClear(1);

        renderer.renderer.setSize(vec2.x, vec2.y);

        shadowComposer.renderTarget1.dispose();
        shadowComposer.renderTarget2.dispose();

        tracker.dispose();
    }
}

