export default class MenuBar {
    constructor(data, onItemClick) {
        this.elem = document.createElement('div');
        this.elem.className = data.className;
        this.itemMap = new Map();

        data.items.map((item, i) => {
            const but = document.createElement('button');
            but.style.zIndex = '100';
            but.className = item.className ?? 'ezv-menu-bar-button';

            but.addEventListener('pointerdown', e => {
                e.stopPropagation();
                
                onItemClick(item);
            })

            const img = document.createElement('img');
            img.src = item.selected ? item.src1 : item.src;

            but.appendChild(img);

            item.elem = but;
            item.img = img;

            this.itemMap.set(item.id, item);

            this.elem.appendChild(but);
        });
    }

    show(show) {
        this.elem.style.visibility = show ? 'visible' : 'hidden'; 
    }

    showItem(id, show) {
        this.getItem(id).elem.style.display = show ? 'block' : 'none';
    }

    getItem(id) {
        return this.itemMap.get(id);
    }

    selectItem(id, sel) {
        const it = this.getItem(id);

        it.selected = sel;

        it.img.src = it.selected ? it.src : it.src1;
    }

    toggleSelectItem(id) {
        this.selectItem(id, !this.getItem(id).selected);
    }

    toggleVisible() {
        this.show(this.elem.style.visibility === 'hidden');
    }
}