import { Vector2, Vector3 } from "./ezyvuecore/three/build/three";
import { Viewer } from "./viewer";

const vec3 = new Vector3();
const vec2 = new Vector2();
const vec21 = new Vector2();

class DomElementPool {
    constructor(size) {
        this.domPool = [];

        for(var i = 0; i < size; ++i)
            this.domPool.push(document.createElement('div'));
        
        this.count = 0;
    }

    allocate() {
        if(this.count === this.domPool.length)
            this.domPool.push(document.createElement('div'));
    
        return this.domPool[this.count++];
    }

    reset() {
        this.count = 0;
    }
}

export default class AnnotationLabels {
    constructor(containerElem, settings) {
        this.containerElem = containerElem;
        this.rootElem = document.createElement('div');
        this.settings = settings;
        
        this.domElemPool = new DomElementPool(16);

        containerElem.appendChild(this.rootElem);
    }

    createLabelsFromScene(scene) {
        const labels = [];

        this.domElemPool.reset();

        while (this.rootElem.firstChild)
            this.rootElem.removeChild(this.rootElem.lastChild);

        scene.traverse(o => {
            if (o.isPoints) {
                const label = this.domElemPool.allocate();

                label.style.position = 'absolute';
                label.style.visibility = o.userData.selected ? 'visible': 'hidden';
                label.style.top = '0';
                label.style.left = '0';
                label.style.zIndex = '1';
                label.style.marginLeft = '0px';
                label.style.marginTop = '0px';
                label.style.padding = '1em';
                label.style.width = '200px';
                label.style.color = '#fff';
                label.style.background = 'rgba(0, 0, 0, 0.8)';
                label.style.borderRadius = '.5em';
                label.style.fontSize = '12px';
                label.style.lineHeight = '1.2';
                label.style.transition = 'opacity .5s';
                label.style.overflowX = 'hidden';
                label.style.overflowWrap = 'break-word';
                label.style.whiteSpace = 'pre-wrap';

                label.textContent = o.userData.labelText;

                labels.push({
                    elem: label,
                    object: o
                })

                this.rootElem.appendChild(label);
            }
        });

        return labels;
    }

    updateLabels(scene, camera) {
        const labels = this.createLabelsFromScene(scene);

        labels.forEach((label, i) => {
            label.object.getWorldPosition(vec3);
            vec3.project(camera);

            if (vec3.z < 1) {
                const x = Math.round((0.5 + vec3.x / 2) * this.containerElem.clientWidth);
                const y = Math.round((0.5 - vec3.y / 2) * this.containerElem.clientHeight);

                label.elem.style.left = x + this.settings.size / 2 + 'px';
                label.elem.style.top = y + this.settings.size / 2 + 'px';
            }
            else {
                label.elem.style.left = '-1000px';
            }
        });
    }

    selectTest(scene, x, y, camera) {
        let selected = null;

        const hitRadius = this.settings.size/2;

        vec21.set(x, y);

        scene.traverse(o => {
            if (o.isPoints) {
                o.userData.selected = false;

                o.getWorldPosition(vec3);

                vec3.project(camera);
                if (vec3.z > 1)
                    return null;

                vec2.set(Math.round((0.5 + vec3.x / 2) * this.containerElem.clientWidth),
                    Math.round((0.5 - vec3.y / 2) * this.containerElem.clientHeight));

                const dist = vec2.distanceTo(vec21);
                if (dist < hitRadius) {
                    o.userData.selected = true;
                    selected = o;
                }
            }
        });

        return selected;
    }
}